import { FC, memo } from "react";
import Countdown from 'react-countdown';

import PoolCountdown from "./PoolCountdown";

type Props = {
  status?: "incoming" | "inprogress" | "ended" | "canceled";
  startTime?: string;
  endTime?: string;
};

const PoolItemStatus: FC<Props> = ({ status, startTime, endTime }) => {
  return (
    <div className="countdown is-flex-grow-1 is-flex-direction-column">
      {status === 'incoming' && (
        <>
          <div>Sale Starts In:</div>
          <Countdown
            date={Number(startTime || 0) * 1000}
            renderer={({ days, hours, minutes, seconds }) => <PoolCountdown days={days} hours={hours} minutes={minutes} seconds={seconds} />}
          />
        </>
      )}
      {status === 'inprogress' && (
        <>
          <div>Sale Ends In:</div>
          <Countdown
            date={Number(endTime || 0) * 1000}
            renderer={({ days, hours, minutes, seconds }) => <PoolCountdown days={days} hours={hours} minutes={minutes} seconds={seconds} />}
          />
        </>
      )}
      {status === 'ended' && (
        <>
          <div>Presale:</div>
          <div>Ended</div>
        </>
      )}
      {status === 'canceled' && (
        <>
          <div>Presale:</div>
          <div>Canceled</div>
        </>
      )}
    </div>
  );
};

export default memo(PoolItemStatus);
