import { FC, memo } from "react";
import Web3 from 'web3';
import useNativeToken from "../../hooks/useNativeToken";

type Props = {
  hardCap?: string;
  totalRaised?: string;
};

const PoolItemProgress: FC<Props> = ({ hardCap, totalRaised }) => {
  const { symbol } = useNativeToken();
  return (
    <div className="content-progress">
      <p className="title">Progress:</p>
      <progress className="progress is-success mt-2 mb-2" value={hardCap && Number(hardCap) !== 0 ? ((Number(totalRaised) / Number(hardCap)) * 100) : 0} max="100">{hardCap && Number(hardCap) !== 0 ? ((Number(totalRaised) / Number(hardCap)) * 100) : 0}%</progress>
      <div className="sub-title is-flex is-align-items-center">
        <div className="is-flex-grow-1">{totalRaised ? Web3.utils.fromWei(totalRaised || '0') : 0} {symbol}</div>
        <div className="is-flex-grow-1 has-text-right">{hardCap ? Web3.utils.fromWei(hardCap || '0') : 0} {symbol}</div>
      </div>
    </div>
  );
};

export default memo(PoolItemProgress);
