import { useEffect, useState } from "react";

import { useWeb3 } from "../packages/web3/hooks";
import { usePoolFactoryContract } from "./useContract";

export default function useSupportedRouter() {
  const web3 = useWeb3();
  const contract = usePoolFactoryContract(web3);
  const [routers, setRouters] = useState<string[]>();
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    if (!web3 || !contract) return;
    (async () => {
      setLoading(true);
      try {
        const r = await contract.methods.getSupportedRouters().call();
        setRouters(r);
      } catch (e) {
        //
      } finally {
        setLoading(false);
      }
    })();
  }, [web3, contract]);

  return {
    routers,
    loading,
  };
}
