import { useState, memo, FC } from "react";

import Logo from '../../assets/images/pinkswap.png';

type Props = {
  url?: string;
};

const PoolImage: FC<Props> = ({ url }) => {
  const [error, setError] = useState(false);
  const onError = () => setError(true);
  return (
    <figure className="media-left">
      <p className="image is-48x48">
        <img
          style={{ filter: error ? 'grayscale(1)' : 'grayscale(0)' }}
          src={error ? Logo : url}
          alt=""
          onError={onError}
        />
      </p>
    </figure>
  );
};

export default memo(PoolImage);
