import { useEffect, useState } from "react";
import Web3 from "web3";
import { parseUnits } from "@ethersproject/units";

import { useWeb3 } from "../packages/web3/hooks";
import { usePoolFactoryContract } from "./useContract";

// @ts-ignore
const BN = Web3.utils.BN;

export default function useTotalToken(
  hardCap: string,
  rate: string,
  pancakeListingRate: string,
  liquidityPercentage: string,
  tokenDecimals?: string,
  totalVestingAmount?: string
) {
  const web3 = useWeb3();
  const contract = usePoolFactoryContract(web3);
  const [total, setTotal] = useState<string>();

  useEffect(() => {
    if (
      !hardCap ||
      !rate ||
      !pancakeListingRate ||
      !liquidityPercentage ||
      !contract ||
      !tokenDecimals
    )
      return;
    (async () => {
      try {
        const presaleRateBn = new BN(
          parseUnits(rate.toString(), Number(tokenDecimals)).toString()
        );
        const pancakeSwapRate = new BN(
          parseUnits(
            pancakeListingRate.toString(),
            Number(tokenDecimals)
          ).toString()
        );
        const t = await contract.methods
          .calculateTotalTokens(
            Web3.utils.toWei(hardCap),
            presaleRateBn.toString(),
            pancakeSwapRate.toString(),
            liquidityPercentage,
            totalVestingAmount || "0"
          )
          .call();
        setTotal(t);
      } catch (e) {
        // console.log(e)
      }
    })();
  }, [
    hardCap,
    rate,
    pancakeListingRate,
    liquidityPercentage,
    contract,
    tokenDecimals,
    totalVestingAmount,
  ]);

  return total;
}
