import { FC } from "react";

type Props = {
  name: string;
  icon: string;
  activating: boolean;
  connected: boolean;
  account?: string | null;
  onClick(): void;
};

const ProviderItem: FC<Props> = ({ icon, name, activating, onClick }) => {
  // @ts-ignore
  const isMetaMaskEnabled = typeof window.ethereum !== 'undefined' || typeof window.web3 !== 'undefined';
  if (name.toLowerCase() === 'metamask' && !isMetaMaskEnabled) {
    return (
      <a href="https://metamask.io/" target="_blank" rel="nofollow noreferrer" className="provider-item">
        <div className="title">Install Metamask</div>
        <img
          src={icon}
          alt=""
          className="icon"
        />
      </a>
    );
  }
  return (
    <div className="provider-item" onClick={onClick}>
      <div className="title">{activating ? 'connecting...' : name}</div>
      {
        activating ? (
          <div>
            <div className="loader" />
          </div>
        ) : (
          <img
            src={icon}
            alt=""
            className="icon"
          />
        )
      }
    </div>
  );
}

export default ProviderItem;
