import { useMemo, FC, memo } from "react";
import { Tooltip } from "antd";
import { AiOutlineLock } from "react-icons/ai";
import { BsCircleFill } from 'react-icons/bs';

type Props = {
  status?: 'inprogress' | 'incoming' | 'canceled' | 'ended';
  useWhitelisting?: boolean;
}

const PoolItemStatusBadge: FC<Props> = ({ status, useWhitelisting }) => {
  const statusInfo = useMemo(() => {
    if (!status) return undefined;
    return {
      ended: {
        txt: ' Sale Ended',
        cx: 'ended'
      },
      inprogress: {
        txt: ' Sale Live',
        cx: 'inprogress'
      },
      incoming: {
        txt: ' Upcoming',
        cx: 'incoming'
      },
      canceled: {
        txt: ' Canceled',
        cx: 'canceled'
      },
    }[status]
  }, [status]);
  if (statusInfo) {
    return (
      <span className={`is-flex status-dot ${statusInfo.cx}`}>
        {typeof useWhitelisting !== 'undefined' && useWhitelisting ? (
          <Tooltip title="Whitelist only">
            <AiOutlineLock size={16} />
          </Tooltip>
        ) : <BsCircleFill />}
        <span>{statusInfo.txt}</span>
      </span>
    );
  }
  return null;
};

export default memo(PoolItemStatusBadge);
