import axios from "axios";
import configs from "../configs";

async function getTrending(chanId: number) {
  try {
    return (
      await axios.get(`${configs.API_BASE_URL}/trending/top?chain_id=${chanId}`)
    ).data?.list || [];
  } catch (e) {
    return [];
  }
}

const TrendingService = {
  getTrending,
};

export default TrendingService;
