/* eslint-disable jsx-a11y/anchor-is-valid */
import { FC } from "react";
import { Card, List, Avatar, Button, Tabs, Spin, Typography } from 'antd';
import range from 'lodash/range';
import { decodePoolDetailData, etherJsFormat, formatDate, getUTCTimeByOffset, getWeekNumber, weeksBetween } from "../utils/helper";
import useLeaderboard from "../hooks/useLeaderboard";
import ILeaderboard from "../types/ILeaderboad";
import { useState } from "react";
import { Link } from "react-router-dom";
import getPath, { RouteNames } from "../utils/path";
import useNativeToken from "../hooks/useNativeToken";
import useBlacklistPool from "../hooks/useBlacklistPool";
import useLeaderboardBlacklist from "../hooks/useLeaderboardBlacklist";
import { useMemo } from "react";

type TLeaderboard = Omit<ILeaderboard, 'pool_detail'> & {
  pool_detail: {
    logoUrl: string;
  };
};

const LeaderboardPage: FC = () => {
  const [miliseconds, setMiliseconds] = useState(+new Date());
  const { loading, value } = useLeaderboard(miliseconds);
  const nativeToken = useNativeToken();
  const blacklistPools = useBlacklistPool();
  const blacklistLeaderboards = useLeaderboardBlacklist();
  const blacklist = useMemo(() => {
    return [
      ...(blacklistLeaderboards || []),
      ...(blacklistPools || []),
    ];
  }, [blacklistPools, blacklistLeaderboards]);
  const leaderboards = value && value.docs && Array.isArray(value.docs) ? value.docs : [];
  const data = leaderboards.map((v: ILeaderboard) => ({
    ...v,
    pool_detail: decodePoolDetailData(v.pool_detail),
  })).filter((v: ILeaderboard) => blacklist.indexOf(v.pool_address) < 0);
  const weeks = weeksBetween(+new Date(2021, 5, 20), +new Date()); // now -> 20/06/2021
  const getOffset = (index: number | string) => {
    const now = +new Date();
    const offset = Number(index) * 7 * 24 * 60 * 60 * 1000; // milisecs in a week * num of week
    return now - offset;
  }
  const handleTabChange = (tabIndex: string) => {
    setMiliseconds(getOffset(tabIndex));
  };
  return (
    <div className="py-6 container">
      <Card>
        <Tabs defaultActiveKey="0" onChange={handleTabChange}>
          {range(weeks).map((v) => {
            const date = new Date(getOffset(v));
            const [year, week] = getWeekNumber(date);
            return (
              <Tabs.TabPane tab={`Week ${week}/${year}`} key={v} />
            );
          })}
        </Tabs>
        <Spin spinning={loading}>
          <List
            itemLayout="horizontal"
            dataSource={data}
            renderItem={(item: TLeaderboard, i: number) => (
              <List.Item key={item.pool_address}>
                <div className="mr-4">
                  <Typography.Title level={4}>{i + 1}</Typography.Title>
                </div>
                <List.Item.Meta
                  avatar={<Avatar size={{ xs: 24, sm: 32, md: 36, lg: 44, xl: 44, xxl: 44 }} src={item.pool_detail.logoUrl} />}
                  title={(
                    <div>
                      <span>{item.token_name}</span>
                      <span className="ml-2 is-size-7">{item.token_symbol}</span>
                    </div>
                  )}
                  description={(
                    <div className="flex items-center">
                      <p>
                        <span>Hardcap: </span>
                        <a>{etherJsFormat(item.hard_cap, nativeToken.decimals || 0)} {nativeToken.symbol}</a>
                      </p>
                      <p>&nbsp;–&nbsp;</p>
                      <p>
                        <span>Listing time: </span>
                        <a>{formatDate(getUTCTimeByOffset(item.finish_time))}</a>
                      </p>
                    </div>
                  )}
                />
                <div className="mr-2">
                  <div className="flex-wrapper">
                    <div className="single-chart">
                      <svg viewBox="0 0 36 36" className="circular-chart green">
                        <path className="circle-bg"
                          d="M18 2.0845
          a 15.9155 15.9155 0 0 1 0 31.831
          a 15.9155 15.9155 0 0 1 0 -31.831"
                        />
                        <path className="circle"
                          stroke-dasharray={`${item.raised_percent}, 100`}
                          d="M18 2.0845
          a 15.9155 15.9155 0 0 1 0 31.831
          a 15.9155 15.9155 0 0 1 0 -31.831"
                        />
                        <text x="18" y="20.35" className="percentage">{item.raised_percent.toString().length > 3 ? item.raised_percent.toFixed(1) : item.raised_percent}%</text>
                      </svg>
                    </div>
                  </div>
                </div>
                <Link to={getPath(RouteNames.LauchpadDetail).replace(':id', item.pool_address)}>
                  <Button type="primary" size="middle">View pool</Button>
                </Link>
              </List.Item>
            )}
          />
        </Spin>
      </Card>
    </div>
  );
};

export default LeaderboardPage;
