import { useMemo } from "react";
import Web3 from "web3";
import configs from "../configs";
import ChainIds from "../configs/chainIds";
import { getContract } from "../packages/web3/helper";
import { useChainId } from "../packages/web3/hooks";

const poolAbi = require("../abi/PoolContract.json");
const poolAbiV2 = require("../abi/PoolContractV2.json");
const erc20Abi = require("../abi/ERC20Abi.json");
const excludeAbi = require("../abi/CheckExcludeAbi.json");

export function usePoolContract(address?: string, web3?: any): any {
  return useMemo(
    () => getContract(poolAbi, address || "", web3),
    [address, web3]
  );
}

export function usePoolV2Contract(address?: string, web3?: any): any {
  return useMemo(
    () => getContract(poolAbiV2, address || "", web3),
    [address, web3]
  );
}

export function useBnbBusdContract() {
  return useMemo(
    () =>
      getContract(
        configs.contract.wbnbBusdPair.bsc.ABI,
        configs.contract.wbnbBusdPair.bsc.ADDRESS
      ),
    []
  );
}

export function usePoolFactoryContract(web3?: any) {
  const chainId = useChainId();
  return useMemo(() => {
    switch (chainId) {
      case ChainIds.BSC_TESTNET:
        return getContract(
          configs.contract.factory.bscTestnet.ABI,
          configs.contract.factory.bscTestnet.ADDRESS || "",
          web3
        );
      case ChainIds.MATIC_TESTNET:
        return getContract(
          configs.contract.factory.maticTestnet.ABI,
          configs.contract.factory.maticTestnet.ADDRESS || "",
          web3
        );
      case ChainIds.KCC_TESTNET:
        return getContract(
          configs.contract.factory.kccTestnet.ABI,
          configs.contract.factory.kccTestnet.ADDRESS || "",
          web3
        );
      default:
        return getContract(
          configs.contract.factory.bsc.ABI,
          configs.contract.factory.bsc.ADDRESS || "",
          web3
        );
    }
  }, [web3, chainId]);
}

