export function usePoolFactoryAddress() {
  const chainId = useChainId();
  return useMemo(() => {
    switch (chainId) {
      case ChainIds.BSC_TESTNET:
        return configs.contract.factory.bscTestnet.ADDRESS || "";
      case ChainIds.MATIC_TESTNET:
        return configs.contract.factory.maticTestnet.ADDRESS || "";
      case ChainIds.KCC_TESTNET:
        return configs.contract.factory.kccTestnet.ADDRESS || "";
      default:
        return configs.contract.factory.bsc.ADDRESS || "";
    }
  }, [chainId]);
}

