export function useErc20Contract(address?: string, web3?: any) {
  return useMemo(() => {
    if (!Web3.utils.isAddress(address || "")) return null;
    return getContract(erc20Abi, address || "", web3);
  }, [web3, address]);
}

export function useExcludeContract(address?: string, web3?: any) {
  return useMemo(
    () => getContract(excludeAbi, address || "", web3),
    [web3, address]
  );
}

