/* eslint-disable react-hooks/exhaustive-deps */
import { useWeb3React } from "@web3-react/core";
import { useCallback, useEffect, useMemo, useState } from "react";
import chunk from "lodash/chunk";
import { useChainId, useWeb3 } from "../packages/web3/hooks";
import IPoolInfo from "../types/IPoolInfo";
import {
  usePoolFactoryContract,
  usePoolFactoryManagerContract,
  useTokenManagerContract,
} from "./useContract";
import usePoolState from "./usePoolState";
import IBlacklistPool from "../types/IBlacklistPool";
import { poolWithInfo } from "../utils/pool";
import AppService from "../services";
import useBlockHeight from "./useBlockHeight";
import useTestnet from "./useTestnet";
import useNativeToken from "./useNativeToken";

let timer: any = null;
let firstLoad = true;
let pageLoading = false;

interface IPool extends IPoolInfo {
  tokenInfo?: {
    name: string;
    decimals: string;
    symbol: string;
    totalSupply: string;
    isTokenGenerated: boolean;
  };
}

export default function usePools(page: number, pageSize = 18) {
  const web3 = useWeb3();
  const blockHeight = useBlockHeight();
  const contract = usePoolFactoryManagerContract(web3);
  const factoryContract = usePoolFactoryContract(web3);
  const tokenManagerContract = useTokenManagerContract(web3);
  const chainId = useChainId();
  const isTestnet = useTestnet();
  const nativeToken = useNativeToken();
  const { account } = useWeb3React();
  const [poolAddresses, setPoolAddresses] = useState<string[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [, setPools] = usePoolState();

  const poolWithPage = useMemo<string[]>(() => {
    const pools = chunk(poolAddresses, pageSize);
    return pools[page - 1] || [];
  }, [poolAddresses, page, pageSize]);

  const getPools = useCallback(
    (addresses: string[]) => {
      (async () => {
        if (addresses.length === 0) return;
        if (firstLoad || pageLoading) {
          setLoading(true);
        }
        try {
          const data: { [address: string]: Partial<IPool> } = {};
          const promises = [];
          for (let i = 0, len = addresses.length; i < len; i++) {
            promises.push(
              poolWithInfo(
                addresses[i],
                web3,
                tokenManagerContract,
                factoryContract,
                account,
                chainId
              )
            );
          }
          const fetchedPools = await Promise.all(promises);
          fetchedPools.map((info) => {
            // const trusted = new AppService.trustScore({
            //   isTokenMintByPinksale: info.token?.isTokenGenerated,
            //   refundType: info.pool?.refundType,
            //   maxBuy: info.pool?.maxContribution,
            //   hardcap: info.pool?.hardCap,
            //   listingRate: info.pool?.liquidityListingRate,
            //   presaleRate: info.pool?.rate,
            //   liquidityPercentage: info.pool?.liquidityPercent,
            //   liquidityLockupTime: isTestnet
            //     ? `${Number(info.pool?.liquidityLockDays) / 60}`
            //     : `${Number(info.pool?.liquidityLockDays) / 86400}`,
            //   totalVestingToken: info.token?.poolBalance || "0",
            //   totalToken: info.token?.totalSupply,
            //   vestingTime: isTestnet
            //     ? `${Number(info.pool?.tgeLockDuration) / 60}`
            //     : `${Number(info.pool?.tgeLockDuration) / 86400}`,
            //   additionalInfo: {
            //     name: info.token?.name,
            //     logo: info.pool?.poolDetails?.logoUrl || "",
            //     description: info.pool?.poolDetails?.description || "",
            //     website: info.pool?.poolDetails?.website || "",
            //     twitter: info.pool?.poolDetails?.twitter || "",
            //     github: info.pool?.poolDetails?.github || "",
            //     telegram: info.pool?.poolDetails?.telegram || "",
            //   },
            //   nativeToken: nativeToken.symbol,
            //   nativeTokenDecimals: nativeToken.decimals,
            //   tokenSymbol: info.token?.symbol,
            //   tokenDecimals: Number(info.token?.decimals),
            // });
            data[info.address] = {
              ...info.pool,
              ...info.additionalInfo,
              tokenInfo: info.token,
              address: info.address,
              // trustedScores: trusted.getTotalScore(),
            };
            return null;
          });
          setPools(data);
        } catch (e) {
          // console.log(e);
        } finally {
          setLoading(false);
          firstLoad = false;
          pageLoading = false;
        }
      })();
    },
    [
      contract,
      chainId,
      web3,
      tokenManagerContract,
      account,
      nativeToken,
      isTestnet,
    ]
  );

  useEffect(() => {
    if (!contract) return;
    clearTimeout(timer);
    timer = setTimeout(() => {
      (async () => {
        try {
          setLoading(true);
          const blacklist = await AppService.pool.getBlacklistPools();
          const blacklistByChain = blacklist
            .filter((p: IBlacklistPool) => p.chainId === chainId)
            .map((p: IBlacklistPool) => p.address);
          const result = await contract.methods.getAllPools().call();
          const enabledPools =
            blacklistByChain.length > 0
              ? result.filter((v: string) => blacklistByChain.indexOf(v) < 0)
              : result;
          const pools = [...enabledPools];
          pools.reverse();
          setPoolAddresses(pools);
        } catch (e) {
          //
        } finally {
          setLoading(false);
        }
      })();
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  }, [contract, chainId, blockHeight]);

  useEffect(() => {
    if (firstLoad) return;
    firstLoad = true;
  }, [page, chainId]);

  useEffect(() => {
    if (pageLoading) return;
    pageLoading = true;
  }, [page]);

  useEffect(() => {
    getPools(poolWithPage);
  }, [poolWithPage]);

  return {
    pools: poolAddresses,
    loading,
  };
}
