import { useWeb3React } from "@web3-react/core";
import { useEffect, useState } from "react";

import { useWeb3 } from "../packages/web3/hooks";
import { useErc20Contract } from "./useContract";

export default function useTokenBalance(address?: string) {
  const web3 = useWeb3();
  const { account } = useWeb3React();
  const contract = useErc20Contract(address, web3);
  const [balance, setBalance] = useState<string>();

  useEffect(() => {
    if (!address || !contract || !account) return;
    (async () => {
      try {
        const bl = await contract.methods.balanceOf(account).call();
        setBalance(bl);
      } catch (e) {
        //
      }
    })();
  }, [address, contract, account]);

  return balance;
}
