import { FC, useCallback, useState } from "react";
import * as Yup from 'yup';
import { FormikHelpers, useFormik } from 'formik';
import { message } from "antd";
import { useWeb3React } from "@web3-react/core";

import Button from "../common/Button";
import FormError from "../common/FormError";
import { useWeb3 } from "../../packages/web3/hooks";
import { usePoolV2Contract } from "../../hooks/useContract";

type TForm = {
  users: string;
};

type Props = {
  address: string;
  type?: 'Add' | 'Remove';
};

const validationSchema: Yup.SchemaOf<TForm> = Yup.object().shape({
  users: Yup.string()
    .required()
    .min(42)
    .max(42 * 1000)
    .test({
      message: 'Max 1000 users',
      test: (val) => {
        return (val || '').split('\n').length <= 1000;
      }
    }), // 1000 address
}).defined();

const WhitelistForm: FC<Props> = ({ address, type = 'Add' }) => {
  const [loading, setLoading] = useState<boolean>(false);
  const { account } = useWeb3React();
  const web3 = useWeb3();
  const contract = usePoolV2Contract(address, web3);
  const handleSubmit = useCallback((values: TForm, formikHelper: FormikHelpers<TForm>) => {
    (async () => {
      try {
        setLoading(true);
        if (type === 'Remove') {
          await contract.methods.removeWhitelistedUsers(values.users.split('\n')).send({
            from: account,
          });
        } else {
          await contract.methods.addWhitelistedUsers(values.users.split('\n')).send({
            from: account,
          });
        }
        message.success(`${type} users successfully`);
        formikHelper.setFieldValue('users', '');
        formikHelper.setFieldError('users', '');
        formikHelper.setFieldTouched('users', false);
      } catch (e) {
        message.error(e?.message || e.toString())
      } finally {
        setLoading(false);
      }
    })();
  }, [account, contract, type]);
  const formik = useFormik<TForm>({
    initialValues: {
      users: '',
    },
    validationSchema,
    validateOnMount: true,
    onSubmit: handleSubmit,
  });
  return (
    <form onSubmit={formik.handleSubmit}>
      <div className="field">
        <label htmlFor="users" className="label">Users</label>
        <div className="control">
          <textarea
            name="users"
            id="users"
            className="textarea"
            rows={10}
            placeholder="Insert address, separate with comma"
            value={formik.values.users}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          ></textarea>
        </div>
        <FormError
          touched={Boolean(formik.touched.users && formik.errors.users)}
          error={formik.errors.users}
        />
      </div>
      <div className="field has-text-centered">
        <Button type="submit" primary disabled={!formik.isValid} loading={loading}>{type} users</Button>
      </div>
    </form>
  );
};

export default WhitelistForm;
