import Web3 from "web3";
import { Interface } from "@ethersproject/abi";
import { getContract } from "../packages/web3/helper";
import configs from "../configs";
import ChainIds from "../configs/chainIds";

interface Call {
  address: string; // Address of the contract
  name: string; // Function name on the contract (example: balanceOf)
  params?: any[]; // Function params
}

interface MulticallOptions {
  web3?: Web3;
  blockNumber?: number;
  requireSuccess?: boolean;
}

const multicall = async (
  abi: any[],
  calls: Call[],
  options: MulticallOptions = {},
  chainId?: number,
) => {
  try {
    let multi = getContract(configs.contract.multicall.bsc.ABI, configs.contract.multicall.bsc.ADDRESS || '', options.web3);
    if (chainId === ChainIds.BSC_TESTNET) {
      multi = getContract(configs.contract.multicall.bscTestnet.ABI, configs.contract.multicall.bscTestnet.ADDRESS || '', options.web3);
    }
    if (chainId === ChainIds.MATIC_TESTNET) {
      multi = getContract(configs.contract.multicall.maticTestnet.ABI, configs.contract.multicall.maticTestnet.ADDRESS || '', options.web3);
    }
    if (chainId === ChainIds.KCC_TESTNET) {
      multi = getContract(configs.contract.multicall.kccTestnet.ABI, configs.contract.multicall.kccTestnet.ADDRESS || '', options.web3);
    }
    const itf = new Interface(abi);

    const calldata = calls.map((call) => [
      call.address.toLowerCase(),
      itf.encodeFunctionData(call.name, call.params),
    ]);
    const { returnData } = await multi.methods
      .aggregate(calldata)
      .call(undefined, options.blockNumber);
    const res = returnData.map((call: any, i: number) =>
      itf.decodeFunctionResult(calls[i].name, call)
    );

    return res;
  } catch (error) {
    throw new Error(error);
  }
};

/**
 * Multicall V2 uses the new "tryAggregate" function. It is different in 2 ways
 *
 * 1. If "requireSuccess" is false multicall will not bail out if one of the calls fails
 * 2. The return inclues a boolean whether the call was successful e.g. [wasSuccessfull, callResult]
 */
// TODO: change abi
export const multicallv2 = async (
  abi: any[],
  calls: Call[],
  options: MulticallOptions = {}
): Promise<any> => {
  const multi = getContract(configs.contract.multicall.bsc.ABI, configs.contract.multicall.bsc.ADDRESS || '', options.web3);
  const itf = new Interface(abi);

  const calldata = calls.map((call) => [
    call.address.toLowerCase(),
    itf.encodeFunctionData(call.name, call.params),
  ]);
  const returnData = await multi.methods
    .tryAggregate(
      options.requireSuccess === undefined ? true : options.requireSuccess,
      calldata
    )
    .call(undefined, options.blockNumber);
  const res = returnData.map((call: any, i: number) => {
    const [result, data] = call;
    return result ? itf.decodeFunctionResult(calls[i].name, data) : null;
  });

  return res;
};

export default multicall;
