import { FC, memo, useMemo } from "react";
import useTestnet from "../../hooks/useTestnet";

type Props = {
  liquidityLockDays?: string;
};

const PoolItemLockupTime: FC<Props> = ({ liquidityLockDays }) => {
  const isTestnet = useTestnet();
  const time = useMemo(() => {
    if (!liquidityLockDays) return 0;
    if (isTestnet) {
      return Number(liquidityLockDays) / 60; // minutes
    }
    return Number(liquidityLockDays) / 24 / 60 / 60;
  }, [isTestnet, liquidityLockDays]);
  return (
    <div className="lockup-time">
      <div className="is-flex is-align-items-center">
        <p className="is-flex-grow-1">Lockup Time:</p>
        <span className="is-flex-grow-1 has-text-right time-text">{time} {!isTestnet ? 'days' : 'minutes'}</span>
      </div>
    </div>
  );
};

export default memo(PoolItemLockupTime);
