const poolDetailMap: any = {
  a: "logoUrl",
  b: "website",
  c: "facebook",
  d: "twitter",
  e: "github",
  f: "telegram",
  g: "reddit",
  h: "description",
  i: "instagram",
  k: "discord",
};

export function formatAddress(address: string): string {
  return `${address.substring(0, 4)}...${address.substring(
    address.length - 4
  )}`;
}

export function numberFormat(num: number) {
  return new Intl.NumberFormat().format(num);
}

export function etherJsFormat(num: string, decimals?: number): string {
  const formated = formatUnits(num, decimals);
  return Number(
    formated.endsWith(".0") ? formated.replace(".0", "") : formated
  ).toLocaleString("en-US");
}

export function toWei(amount: string, decimals: string) {
  try {
    // return new BN(amount).mul(new BN("10").pow(new BN(decimals))).toString();
    return parseUnits(amount, decimals).toString();
  } catch (e) {
    return amount;
  }
}

export function formatDate(date: number) {
  return format(date, "yyyy.MM.dd HH:mm");
}

export function encodePoolDetailData(data: IPoolDetail): string {
  const json = Object.keys(data).reduce((acc, current) => {
    // @ts-ignore
    acc[_invert(poolDetailMap)[current]] = data[current];
    return acc;
  }, {});
  return JSON.stringify(json);
}

export function decodePoolDetailData(data: string): IPoolDetail {
  try {
    const obj = JSON.parse(data);
    const json = Object.keys(obj).reduce((acc, current) => {
      // @ts-ignore
      acc[poolDetailMap[current]] = obj[current];
      return acc;
    }, {});
    return json;
  } catch (e) {
    return {};
  }
}

export function weeksBetween(d1: number, d2: number): number {
  return Math.round((d2 - d1) / (7 * 24 * 60 * 60 * 1000));
}

export function calcListingFee({
  tokenDecimals,
  tokenFeePercent,
  hardCap,
  presaleRate,
  listingRate,
  liquidityPercentage,
}: {
  tokenDecimals: string | number;
  tokenFeePercent: string | number;
  hardCap: string | number;
  presaleRate: number | string;
  listingRate: number | string;
  liquidityPercentage: number | string;
}) {
  const bnbExponent = new BN("10").pow(new BN("18"));
  const exponent = new BN("10").pow(new BN(tokenDecimals));

  const hcap = new BN(Web3.utils.toWei(hardCap.toString()));
  const presaleRateBn = new BN(presaleRate.toString()).mul(exponent);
  const pancakeSwapRate = new BN(listingRate.toString()).mul(exponent);
  const presaleToken = hcap.mul(presaleRateBn).div(bnbExponent);
  const swapListing = hcap
    .mul(pancakeSwapRate)
    .mul(new BN(liquidityPercentage.toString()))
    .div(new BN("100"))
    .div(bnbExponent);
  const fee = presaleToken
    .mul(new BN(tokenFeePercent.toString()))
    .div(new BN("100"));
  // const totalSale = presaleToken.add(swapListing);
  // const total = presaleToken.add(swapListing).add(fee);
  return {
    total: presaleToken.add(swapListing).toString(),
    fee: fee.toString(),
    totalSale: presaleToken.toString(),
  };
}

export function getUTCTimestamp() {
  const now = new Date();
  const offset = now.getTimezoneOffset() * 60 * 1000;
  return Math.floor(+now + offset);
}

export function getUTCTimeByOffset(unixTime: number) {
  const offset = new Date().getTimezoneOffset() * 60; // s
  return Math.abs(unixTime + offset) * 1000;
}

export function getWeekNumber(d: Date) {
  // Copy date so don't modify original
  d = new Date(Date.UTC(d.getFullYear(), d.getMonth(), d.getDate()));
  // Set to nearest Thursday: current date + 4 - current day number
  // Make Sunday's day number 7
  d.setUTCDate(d.getUTCDate() + 4 - (d.getUTCDay() || 7));
  // Get first day of year
  var yearStart = new Date(Date.UTC(d.getUTCFullYear(), 0, 1));
  // Calculate full weeks to nearest Thursday
  // @ts-ignore
  var weekNo = Math.ceil(((d - yearStart) / 86400000 + 1) / 7);
  // Return array of year and week number
  return [d.getUTCFullYear(), weekNo];
}
