/* eslint-disable jsx-a11y/anchor-is-valid */
import { FC, useCallback } from "react";
import Web3 from 'web3';
import { useWeb3React } from '@web3-react/core';
import { Menu, Dropdown } from 'antd';

import { formatAddress } from "../../utils/helper";
import { useBalance } from "../../packages/web3/hooks";
import useNativeToken from "../../hooks/useNativeToken";

const Account: FC = () => {
  const { active, deactivate, account } = useWeb3React();
  const nativeToken = useNativeToken();
  const balance = useBalance();
  const handleDisconnect = useCallback(() => {
    if (active) {
      deactivate();
      return;
    }
  }, [deactivate, active]);
  const menu = (
    <Menu>
      <Menu.Item>
        <div className="navbar-item">
          {parseFloat(Web3.utils.fromWei(balance || '0')).toFixed(6)} {nativeToken.symbol}
        </div>
      </Menu.Item>
      <Menu.Item>
        <div className="navbar-item" onClick={handleDisconnect}>
          Log out
        </div>
      </Menu.Item>
    </Menu>
  );
  if (!active) return null;
  return (
    <Dropdown overlay={menu}>
      <div className="connectButton">
        <span>{formatAddress(account || '-')}</span>
      </div>
    </Dropdown>
  )
}

export default Account;
