import { useEffect } from "react";
import { useState } from "react";

import { useWeb3 } from "../packages/web3/hooks";
import { usePoolFactoryContract } from "./useContract";

export default function usePoolFee() {
  const web3 = useWeb3();
  const contract = usePoolFactoryContract(web3);
  const [fee, setFee] = useState<string>();

  useEffect(() => {
    if (!contract) return;
    (async () => {
      try {
        const f = await contract.methods.fixedEthFee().call();
        setFee(f);
      } catch (e) {
        //
      }
    })();
  }, [contract]);
  return fee;
}
