import { CSSProperties, FC, useMemo } from "react";
import { Layout } from 'antd';

import SidebarMenu from "../components/common/SidebarMenu";
import Footer from "../components/common/Footer";
import useSidebar from "../hooks/useSidebar";
import { useFetchBlacklistPool } from "../hooks/useBlacklistPool";
import configs from "../configs";
import HeaderNavigation from "../components/common/HeaderNavigation";
// @ts-ignore
import { useFetchLeaderboardBlacklist } from "../hooks/useLeaderboardBlacklist";
import Trending from "../components/common/Trending";

const { Header: AntHeader, Sider, Content, Footer: AntFooter } = Layout;

const MainLayout: FC = ({ children }) => {
  const [sidebar, setSidebar] = useSidebar();
  const isMobile = window.innerWidth < configs.MOBILE_BREAKPOINT;
  const layoutStyle = useMemo(() => {
    return {
      marginLeft: sidebar.collapsed ? (isMobile ? 0 : 80) : (isMobile ? 0 : 200),
      padding: '40px 20px 20px',
      transition: 'all 200ms linear',
    };
  }, [sidebar.collapsed, isMobile]);
  const headerStyle = useMemo(() => ({
    background: 'white',
    height: 70,
    position: 'fixed',
    zIndex: 100,
    width: '100%',
    padding: '0 30px',
  }), []);
  const siderStyle = useMemo(() => ({
    background: 'white',
    overflow: 'auto',
    height: '100vh',
    position: 'fixed',
    top: 70,
    left: 0,
    zIndex: 10
  }), []);
  const overlayStyle = useMemo(() => ({
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    background: 'rgba(0, 0, 0, 0.4)',
    zIndex: 9
  }), []);
  const overlayVisible = useMemo(() => {
    return !sidebar.collapsed && isMobile;
  }, [sidebar.collapsed, isMobile]);
  const closeSidebar = () => setSidebar({ collapsed: true });

  useFetchBlacklistPool();
  useFetchLeaderboardBlacklist();
  return (
    <Layout>
      <AntHeader style={headerStyle as CSSProperties}>
        <HeaderNavigation />
      </AntHeader>
      <Sider
        style={siderStyle as CSSProperties}
        breakpoint="lg"
        collapsedWidth={isMobile ? 0 : 80}
        trigger={null}
        collapsible
        collapsed={sidebar.collapsed}
      >
        <SidebarMenu />
      </Sider>
      {overlayVisible && (
        <div
          style={overlayStyle as CSSProperties}
          onClick={closeSidebar}
        />
      )}
      <Layout style={layoutStyle}>
        <Content>
          <Trending />
          {children}
        </Content>
        <AntFooter>
          <Footer />
        </AntFooter>
      </Layout>
    </Layout>
  );
};

export default MainLayout;
