import { ChangeEvent, FC, useState, useMemo } from "react";
import { Spin, Pagination, Input, Select } from 'antd';
import debounce from 'lodash/debounce';
import Web3 from 'web3';

import PoolItem from "./PoolItem";
import useMyPoolState from "../../hooks/useMyPoolState";
import useMyPool from "../../hooks/useMyPool";

const PAGE_SIZE = 18;
const { Option } = Select;

const MyPoolTab: FC = () => {
  const [page, setPage] = useState<number>(1);
  const [options, setOptions] = useState<{
    query?: string;
    filter?: string;
    status?: string;
  }>({
    query: '',
    filter: '',
    status: '',
  });
  const { loading, pools: rawPool } = useMyPool(page, PAGE_SIZE);
  const [poolsRaw] = useMyPoolState();

  const filtered = useMemo(() => {
    let pools = Object.values(poolsRaw);
    if (options.status) {
      pools = pools.filter((pool) => pool.status === options.status);
    };
    if (options.filter) {
      if (options.filter === 'hardcap' || options.filter === 'softcap') {
        pools = pools.sort((pool1, pool2) => {
          const capA = Number(Web3.utils.fromWei(pool1.hardCap || '0'));
          const capB = Number(Web3.utils.fromWei(pool2.hardCap || '0'));
          return capB - capA;
        });
      } else if (options.filter === 'liquidity') {
        pools = pools.sort((pool1, pool2) => {
          const capA = Number(Web3.utils.fromWei(pool1.liquidityPercent || '0'));
          const capB = Number(Web3.utils.fromWei(pool2.liquidityPercent || '0'));
          return capB - capA;
        });
      }
    }
    if (options.query) {
      const regex = new RegExp(options.query, 'gi');
      pools = pools.filter((pool) => {
        const builtQuery = `${pool.address}|${pool.tokenInfo?.name}|${pool.tokenInfo?.symbol}`;
        const condition = regex.test(builtQuery);
        regex.lastIndex = 0;
        return condition;
      });
    }
    return pools;
  }, [poolsRaw, options]);

  const onPageChange = (num: number) => {
    setPage(num);
    setOptions({
      query: '',
      filter: '',
      status: '',
    });
    window.scrollTo({
      top: 100,
      behavior: 'smooth'
    });
  };
  const onQueryChange = (e: ChangeEvent<HTMLInputElement>) => {
    setOptions((prev) => ({
      ...prev,
      query: e.target.value || '',
    }));
  };
  const onQueryChangeDebounce = debounce(onQueryChange, 1000);
  const onStatusChange = (value: string) => {
    setOptions((prev) => ({
      ...prev,
      status: value || '',
    }));
  };
  const onFilterChange = (value: string) => {
    setOptions((prev) => ({
      ...prev,
      filter: value || '',
    }));
  };
  return (
    <Spin spinning={loading}>
      <div className="pool-filter">
        <div className="search">
          <div className="filter-title invisible">s</div>
          <Input
            placeholder="Enter token name or token symbol"
            style={{ borderRadius: 16 }}
            onChange={onQueryChangeDebounce}
            allowClear
          />
        </div>
        <div className="filter">
          <div className="status">
            <div className="filter-title">Filter By</div>
            <Select value={options.status} onChange={onStatusChange}>
              <Option value="">All Status</Option>
              <Option value="incoming">Upcoming</Option>
              <Option value="inprogress">Inprogress</Option>
              <Option value="ended">Ended</Option>
              <Option value="canceled">Canceled</Option>
            </Select>
          </div>

          <div>
            <div className="filter-title">Sort By</div>
            <Select value={options.filter} onChange={onFilterChange}>
              <Option value="">No Filter</Option>
              <Option value="hardcap">Hard Cap</Option>
              <Option value="softcap">Soft Cap</Option>
              <Option value="liquidity">LP percent</Option>
            </Select>
          </div>
        </div>
      </div>
      <div className="columns is-multiline">
        {filtered.map((pool) => (
          <PoolItem
            key={pool.address}
            address={pool.address || ''}
            pool={pool}
          />
        ))}
      </div>
      {filtered.length === 0 && (
        <div className="has-text-centered p-4">No data</div>
      )}
      {rawPool.length > 0 && (
        <div className="has-text-centered">
          <Pagination
            defaultCurrent={1}
            current={page}
            total={rawPool.length}
            pageSize={PAGE_SIZE}
            showSizeChanger={false}
            onChange={onPageChange}
          />
        </div>
      )}
    </Spin>
  );
};

export default MyPoolTab;
