import { useEffect, useState } from "react";

import { useWeb3 } from "../packages/web3/hooks";
import { useExcludeContract } from "./useContract";

let timer: any = null;

export default function useExcludeExists(token?: string, poolAddress?: string) {
  const [showExcludeFromReward, setShowExcludeFromReward] = useState<boolean>(false);
  const [error, setError] = useState<boolean>(false);
  const [showExcludeFromFee, setShowExcludeFromFee] = useState<boolean>(false);
  const web3 = useWeb3();
  const contract = useExcludeContract(token, web3);

  useEffect(() => {
    if (!contract || !token || !poolAddress || error) return;
    clearInterval(timer);
    timer = setInterval(() => {
      (async () => {
        try {
          const [excludeFromFee, excludeFromReward] = await Promise.all([
            contract.methods.isExcludedFromFee(poolAddress).call(),
            contract.methods.isExcludedFromReward(poolAddress).call(),
          ]);
          setShowExcludeFromFee(!excludeFromFee);
          setShowExcludeFromReward(!excludeFromReward);
        } catch (e) {
          setShowExcludeFromReward(false);
          setShowExcludeFromFee(false);
          setError(true);
        }
      })();
    }, 2500);
    return () => {
      clearInterval(timer);
    };
  }, [
    contract,
    token,
    showExcludeFromReward,
    setShowExcludeFromFee,
    poolAddress,
    error,
  ]);
  return {
    showExcludeFromFee,
    showExcludeFromReward,
  };
}
