import { FC } from "react";
import { Button as AntButton } from 'antd';
import { ReactNode } from "react";

type Props = {
  icon?: ReactNode;
  primary?: boolean;
  danger?: boolean;
  loading?: boolean;
  fullwidth?: boolean;
  disabled?: boolean;
  type?: "button" | "submit" | "reset";
  className?: string;
  onClick?(): void;
};

const Button: FC<Props> = ({
  primary = false,
  loading = false,
  fullwidth = false,
  disabled = false,
  danger= false,
  type = "button",
  icon,
  children,
  onClick,
}) => (
  <AntButton
    type={primary ? 'primary' : 'default'}
    icon={icon}
    htmlType={type}
    loading={loading}
    block={fullwidth}
    disabled={disabled}
    danger={danger}
    onClick={onClick}
  >
    {children}
  </AntButton>
);

export default Button;
