export enum RouteNames {
  Home,
  CreateLauchpad,
  LauchpadList,
  LauchpadDetail,
  CreateToken,
  MyTokens,
  EditLauchpad,
  Leaderboard,
}

export default function getPath(name: RouteNames): string {
  return {
    [RouteNames.Home]: '/',
    [RouteNames.CreateLauchpad]: '/launchpad/create',
    [RouteNames.LauchpadList]: '/launchpad',
    [RouteNames.LauchpadDetail]: '/launchpad/:id',
    [RouteNames.EditLauchpad]: '/launchpad/edit/:id',
    [RouteNames.CreateToken]: '/token/create',
    [RouteNames.MyTokens]: '/tokens',
    [RouteNames.Leaderboard]: '/leaderboard',
  }[name];
}
