import { FC } from "react";
import { List, Button } from 'antd';

import useMyTokens from "../../hooks/useMyTokens";

type Props = {
  onSuccess?(address?: string): void;
};

const TokenList: FC<Props> = ({ onSuccess }) => {
  const { tokens, loading } = useMyTokens();
  const list = tokens.map((t) => ({
    title: `${t.name} . ${t.symbol}`,
    description: t.address
  }));
  return (
    <List
      itemLayout="horizontal"
      dataSource={list}
      loading={loading}
      renderItem={(item) => (
        <List.Item actions={[<Button onClick={() => onSuccess?.(item.description)} size="small" type="primary" key={item.description}>Select</Button>]}>
          <List.Item.Meta
            title={item.title}
            description={item.description}
          />
        </List.Item>
      )}
    />
  );
};

export default TokenList;
