/* eslint-disable react-hooks/exhaustive-deps */
import { useWeb3React } from "@web3-react/core";
import { useCallback, useEffect, useMemo, useState } from "react";
import chunk from "lodash/chunk";

import { useChainId, useWeb3 } from "../packages/web3/hooks";
import IPoolInfo from "../types/IPoolInfo";
import {
  usePoolFactoryContract,
  usePoolFactoryManagerContract,
  useTokenManagerContract,
} from "./useContract";
import useMyPoolState from "./useMyPoolState";
import { poolWithInfo } from "../utils/pool";

let timer: any = null;

interface IPool extends IPoolInfo {
  tokenInfo?: {
    name: string;
    decimals: string;
    symbol: string;
    totalSupply: string;
    isTokenGenerated: boolean;
  };
}

export default function useMyPool(page: number, pageSize = 18) {
  const web3 = useWeb3();
  const contract = usePoolFactoryManagerContract(web3);
  const factoryContract = usePoolFactoryContract(web3);
  const tokenManagerContract = useTokenManagerContract(web3);
  const chainId = useChainId();
  const { account } = useWeb3React();
  const [poolAddresses, setPoolAddresses] = useState<string[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [, setPools] = useMyPoolState();

  const poolWithPage = useMemo<string[]>(() => {
    const pools = chunk(poolAddresses, pageSize);
    return pools[page - 1] || [];
  }, [poolAddresses, page, pageSize]);

  const getPools = useCallback(
    (addresses: string[], reload = false) => {
      (async () => {
        if (addresses.length === 0) return;
        if (!reload) {
          setLoading(true);
        }
        try {
          const data: { [address: string]: Partial<IPool> } = {};
          const promises = [];
          for (let i = 0, len = addresses.length; i < len; i++) {
            promises.push(
              poolWithInfo(
                addresses[i],
                web3,
                tokenManagerContract,
                factoryContract,
                account,
                chainId
              )
            );
          }
          const fetchedPools = await Promise.all(promises);
          fetchedPools.map((info) => {
            data[info.address] = {
              ...info.pool,
              ...info.additionalInfo,
              tokenInfo: info.token,
              address: info.address,
            };
            return null;
          });
          setPools(data);
        } catch (e) {
          //
        } finally {
          setLoading(false);
          setTimeout(() => {
            getPools(addresses, true);
          }, 5000);
        }
      })();
    },
    [contract, chainId, web3, tokenManagerContract, account]
  );

  useEffect(() => {
    if (!contract) return;
    clearTimeout(timer);
    timer = setTimeout(() => {
      (async () => {
        setLoading(true);
        try {
          if (!account) return;
          const result = await contract.methods.getPoolsOf(account).call();
          const pools = [...result];
          pools.reverse();
          setPoolAddresses(pools);
        } catch (e) {
          // console.log(e)
        } finally {
          setLoading(false);
        }
      })();
    }, 1000);

    return () => {
      clearTimeout(timer);
    };
  }, [contract, chainId, account]);

  useEffect(() => {
    getPools(poolWithPage);
  }, [poolWithPage]);

  return {
    pools: poolAddresses,
    loading,
  };
}
