import { FC, memo } from "react";

import useNativeToken from "../../hooks/useNativeToken";
import { etherJsFormat } from "../../utils/helper";

type Props = {
  tokenName?: string;
  tokenDecimals?: string;
  tokenSymbol?: string;
  rate?: string;
};

const PoolItemTitle: FC<Props> = ({ tokenName, tokenDecimals, tokenSymbol, rate }) => {
  const { symbol } = useNativeToken();
  return (
    <div className="content-title">
      <p className="title">
        <span>{tokenName || '-'}</span>
      </p>
      {tokenDecimals ? <p className="subtitle">1 {symbol} = {etherJsFormat(rate || '0', Number(tokenDecimals || '0'))} {tokenSymbol}</p> : <p className="subtitle">-</p>}
    </div>
  );
};

export default memo(PoolItemTitle);
