import { FC, useEffect, useState } from "react";
import Web3 from 'web3';

import useWbnbPrice from "../../hooks/useBnbPrice";
import { usePoolFactoryManagerContract } from "../../hooks/useContract";
import { numberFormat } from "../../utils/helper";
import { useWeb3 } from "../../packages/web3/hooks";

type TStatistic = {
  totalLiquidityRaised: string;
  totalProject: string;
  allTimeUniqueParticipants: string;
  totalLiquidityLocked: string;
};

const Statistics: FC = () => {
  const web3 = useWeb3();
  const bnbPrice = useWbnbPrice();
  const factory = usePoolFactoryManagerContract(web3);
  const [statistic, setStatistic] = useState<TStatistic>();

  useEffect(() => {
    if (!factory) return;
    (async () => {
      try {
        const [
          tvlInBnb,
          totalLiquidityRaisedInBnb,
          totalParticipants,
          totalPool
        ] = await Promise.all([
          factory.methods.totalValueLockedInBnb().call(),
          factory.methods.totalLiquidityRaisedInBnb().call(),
          factory.methods.totalParticipants().call(),
          factory.methods.getTotalNumberOfPools().call(),
        ]);
        setStatistic({
          totalLiquidityRaised: numberFormat(Number(Web3.utils.fromWei(totalLiquidityRaisedInBnb)) * Number(bnbPrice)),
          totalProject: totalPool,
          allTimeUniqueParticipants: numberFormat(Number(totalParticipants)),
          totalLiquidityLocked: numberFormat(Number(Web3.utils.fromWei(tvlInBnb)) * Number(bnbPrice)),
        })
      } catch (e) {
        //
      }
    })();
  }, [factory, bnbPrice]);
  return (
    <nav className="stats">
      <div className="stat has-text-centered">
        <div className={"stat-box"}>
          <p className="title">${statistic?.totalLiquidityRaised || 0}</p>
          <p className="heading">Total Liquidity Raised</p>
        </div>
      </div>
      <div className="stat has-text-centered">
        <div className={"stat-box"}>
          <p className="title">{statistic?.totalProject || 0}</p>
          <p className="heading">Total Projects</p>
        </div>
      </div>
      <div className="stat has-text-centered">
        <div className={"stat-box"}>
          <p className="title">{statistic?.allTimeUniqueParticipants || 0}</p>
          <p className="heading">Total Participants</p>
        </div>
      </div>
      <div className="stat has-text-centered">
        <div className={"stat-box"}>
          <p className="title">${statistic?.totalLiquidityLocked || 0}</p>
          <p className="heading">Total Liquidity Locked</p>
        </div>
      </div>
    </nav>
  );
};

export default Statistics;
