import { FC } from "react";
import { IoTrendingUpOutline } from "react-icons/io5";
import { Link } from "react-router-dom";

import useTrending from "../../hooks/useTrending";
import ITrendingPool from "../../types/ITrendingPool";
import getPath, { RouteNames } from "../../utils/path";

import styles from "./Trending.module.css";

const Trending: FC = () => {
  const { value: trending } = useTrending();
  if (!trending || !Array.isArray(trending) || trending.length === 0) return null;
  return (
    <div className={styles.trending}>
      <div className={styles.title}>
        <IoTrendingUpOutline />
        &nbsp;
        <span>Trending</span>
      </div>
      <ul className={styles.items}>
        {trending.map(({ address, token_name }: ITrendingPool, i: number) => (
          <li className={styles.trendingItem} key={address}>
            <span>#{i + 1}</span>
            &nbsp;
            <Link to={getPath(RouteNames.LauchpadDetail).replace(':id', address)}>{token_name}</Link>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Trending;
