/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect, useRef } from "react";
import { UnsupportedChainIdError, useWeb3React } from "@web3-react/core";
import Web3 from "web3";
import { createGlobalState } from "react-use";

import { injected } from "./connectors";
import { getWeb3NoAccount, requestBscNetwork } from "./helper";
import ChainIds from "../../configs/chainIds";
import IcBsc from "../../assets/icons/ic-bsc.svg";
import IcMatic from "../../assets/icons/ic-matic.png";
import IcKcs from "../../assets/icons/ic-kcs.png";
import configs from "../../configs";

type Props = {
  visible: boolean;
};

const shared = createGlobalState<Props>({
  visible: false,
});

export function useConnectModal() {
  const [state, setState] = shared();
  const open = () => setState({ visible: true });
  const close = () => setState({ visible: false });
  return {
    open,
    close,
    visible: state.visible,
  };
}

export function useEagerConnect() {
  const { activate, active } = useWeb3React();
  const [tried, setTried] = useState(false);

  useEffect(() => {
    injected.isAuthorized().then((isAuthorized: boolean) => {
      if (isAuthorized) {
        activate(injected, undefined, true).catch(() => {
          setTried(true);
        });
      } else {
        setTried(true);
      }
    });
  }, []); // intentionally only running on mount (make sure it's only mounted once :))

  // if the connection worked, wait until we get confirmation of that to flip the flag
  useEffect(() => {
    if (!tried && active) {
      setTried(true);
    }
  }, [tried, active]);

  return tried;
}

export function useInactiveListener(suppress: boolean = false) {
  const { active, error, activate } = useWeb3React();

  useEffect((): any => {
    const { ethereum } = window as any;
    if (ethereum && ethereum.on && !active && !error && !suppress) {
      const handleConnect = () => {
        activate(injected);
      };
      const handleChainChanged = (chainId: string | number) => {
        activate(injected);
      };
      const handleAccountsChanged = (accounts: string[]) => {
        if (accounts.length > 0) {
          activate(injected);
        }
      };
      const handleNetworkChanged = (networkId: string | number) => {
        activate(injected);
      };

      ethereum.on("connect", handleConnect);
      ethereum.on("chainChanged", handleChainChanged);
      ethereum.on("accountsChanged", handleAccountsChanged);
      ethereum.on("networkChanged", handleNetworkChanged);

      return () => {
        if (ethereum.removeListener) {
          ethereum.removeListener("connect", handleConnect);
          ethereum.removeListener("chainChanged", handleChainChanged);
          ethereum.removeListener("accountsChanged", handleAccountsChanged);
          ethereum.removeListener("networkChanged", handleNetworkChanged);
        }
      };
    }
  }, [active, error, suppress, activate]);
}

export function useRequestBscNetwork() {
  const { connector, error, activate } = useWeb3React();
  useEffect(() => {
    if (!error || !connector) return;
    if (error instanceof UnsupportedChainIdError) {
      requestBscNetwork().then((requested) => {
        if (requested) {
          activate(connector);
        }
      });
    }
  }, [error, connector, activate]);
}

export function useWeb3() {
  const { library } = useWeb3React();
  const refEth = useRef(library);
  const [web3, setweb3] = useState(
    library ? new Web3(library.provider) : getWeb3NoAccount()
  );

  useEffect(() => {
    if (library !== refEth.current) {
      setweb3(library ? new Web3(library.provider) : getWeb3NoAccount());
      refEth.current = library;
    }
  }, [library]);

  return web3;
}

export function useBalance() {
  const { account, library, chainId } = useWeb3React();
  const [balance, setBalance] = useState();
  useEffect((): any => {
    if (!!account && !!library) {
      let stale = false;
      library
        .getBalance(account)
        .then((balance: any) => {
          if (!stale) {
            setBalance(balance);
          }
        })
        .catch(() => {
          if (!stale) {
            setBalance(undefined);
          }
        });

      return () => {
        stale = true;
        setBalance(undefined);
      };
    }
  }, [account, library, chainId]);

  return balance ? (balance || "").toString() : balance;
}

export function useChainId() {
  const { chainId } = useWeb3React();
  return chainId || configs.CHAIN_ID;
}

export function useChain() {
  const { chainId } = useWeb3React();
  switch (chainId) {
    case ChainIds.BSC_TESTNET:
      return {
        name: "BSC TESTNET",
        icon: IcBsc,
        family: "bsc",
      };
    case ChainIds.BSC_MAINNET:
      return {
        name: "BSC MAINNET",
        icon: IcBsc,
        family: "bsc",
      };
    case ChainIds.MATIC_TESTNET:
      return {
        name: "MUMBAI",
        icon: IcMatic,
        family: "matic",
      };
    case ChainIds.KCC_TESTNET:
      return {
        name: "KCC TESTNET",
        icon: IcKcs,
        family: "kucoin",
      };
    default:
      return {
        name: "Not support",
        icon: null,
        family: null,
      };
  }
}
