import {
  ChangeEvent,
  FC,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react'
import { useRouteMatch } from 'react-router'
import { FiGlobe, FiTwitter, FiGithub } from 'react-icons/fi'
import { RiFacebookCircleLine } from 'react-icons/ri'
import { FaTelegramPlane, FaDiscord } from 'react-icons/fa'
import { MdVerifiedUser } from 'react-icons/md'
import {
  AiOutlineReddit,
  AiOutlineCheck,
  AiOutlineEdit,
  AiOutlineInstagram,
} from 'react-icons/ai'
import Web3 from 'web3'
import { DiscussionEmbed } from 'disqus-react'
import Countdown, { zeroPad } from 'react-countdown'
import { message, Card, Tooltip } from 'antd'
import { Link } from 'react-router-dom'
import moment from 'moment'
import { useWeb3React } from '@web3-react/core'

import usePoolInfo from '../hooks/usePoolInfo'
import useTokenInfo from '../hooks/useTokenInfo'
import SmallLoading from '../components/common/SmallLoading'
import {
  calcListingFee,
  etherJsFormat,
  formatDate,
  getUTCTimeByOffset,
} from '../utils/helper'
import Button from '../components/common/Button'
import { useExcludeContract, usePoolContract } from '../hooks/useContract'
import getPath, { RouteNames } from '../utils/path'
import useExcludeExists from '../hooks/useExcludeExists'
import useExplorerDomain from '../hooks/useExplorerDomain'
import { useWeb3 } from '../packages/web3/hooks'
import useNativeToken from '../hooks/useNativeToken'
import VestingTable from '../components/pools/VestingTable'
import useTestnet from '../hooks/useTestnet'
import useBlacklistPool from '../hooks/useBlacklistPool'
import Logo from '../assets/images/pinkswap.png'
import WhitelistAction from '../components/pools/WhitelistAction'
import WhitelistTable from '../components/pools/WhitelistTable'
import usePoolState from '../hooks/usePoolState'
// import AppService from '../services'
// import TrustScoreTable from '../components/common/TrustScoreTable'
import { usePoolViewer } from '../hooks/usePoolViewer'

const rendererCountdown = ({ days, hours, minutes, seconds }: any) => (
  <div className="has-text-centered">
    <strong>
      <span
        className="p-2 has-background-danger-light mr-2"
        style={{ borderRadius: 4 }}
      >
        {zeroPad(days)}
      </span>
      <span
        className="p-2 has-background-danger-light mr-2"
        style={{ borderRadius: 4 }}
      >
        {zeroPad(hours)}
      </span>
      <span
        className="p-2 has-background-danger-light mr-2"
        style={{ borderRadius: 4 }}
      >
        {zeroPad(minutes)}
      </span>
      <span
        className="p-2 has-background-danger-light mr-2"
        style={{ borderRadius: 4 }}
      >
        {zeroPad(seconds)}
      </span>
    </strong>
  </div>
)

// @ts-ignore
const BN = Web3.utils.BN
let timer: any = null
let timerUpdateId: any = null
let liquidityTimer: any = null

const PoolDetailPage: FC = () => {
  const router = useRouteMatch<{
    id: string
  }>()
  const web3 = useWeb3()
  const { account } = useWeb3React()
  const nativeToken = useNativeToken()
  const explorerDomain = useExplorerDomain()
  const [storagePools] = usePoolState()

  const [updateId, setUpdateId] = useState<number>(0)
  const [amount, setAmount] = useState<string>()
  const [minAmount, setMinAmount] = useState<string>()
  const [maxAmount, setMaxAmount] = useState<string>()
  const [convertAmount, setConvertAmount] = useState<string>()
  const [tab, setTab] = useState<string>('info')
  const [liquidityBalance, setLiquidityBalance] = useState<string>()
  const [submitLoading, setSubmitLoading] = useState<boolean>(false)
  const [cancelLoading, setCancelLoading] = useState<boolean>(false)
  const [KYCLoading, setKYCLoading] = useState<boolean>(false)
  const [isExcludedFromMaxTx, setIsExcludedFromMaxTx] = useState<boolean>()
  const [excludeFromMaxTxLoading, setExcludeFromMaxTxLoading] = useState<
    boolean
  >(false)
  const [excludeFromFeeLoading, setExcludeFromFeeLoading] = useState<boolean>(
    false,
  )
  const [excludeFromRewardLoading, setExcludeFromRewardLoading] = useState<
    boolean
  >(false)
  const [imageError, setImageError] = useState<boolean>(false)
  const [finishLoading, setFinishLoading] = useState<boolean>(false)
  const [claimLoading, setClaimLoading] = useState<boolean>(false)
  const [withdrawalLoading, setWithdrawalLoading] = useState<boolean>(false)
  const [withdrawalLeftOver, setWithdrawalLeftOver] = useState<boolean>(false)
  const [withdrawalLiquidity, setWithdrawalLiquidity] = useState<boolean>(false)
  const { pool: fetchedPool, loading } = usePoolInfo(router.params.id, updateId)
  const pool =
    fetchedPool && fetchedPool.endTime
      ? fetchedPool
      : storagePools[router.params.id]
  const { token, loading: tokenLoading } = useTokenInfo(
    pool?.token,
  );
  const poolContract = usePoolContract(router.params.id, web3)
  const { showExcludeFromFee, showExcludeFromReward } = useExcludeExists(
    pool?.token,
    router.params.id,
  )
  const tokenExcludeContract = useExcludeContract(pool?.token, web3)
  const isTestnet = useTestnet()
  const blacklist = useBlacklistPool()
  const handleAmountChange = (e: ChangeEvent<HTMLInputElement>) => {
    setAmount(e.target.value)
  }
  const excludeFromFee = useCallback(() => {
    if (
      !tokenExcludeContract ||
      !account ||
      account !== pool?.owner ||
      !router.params.id
    )
      return
        ; (async () => {
          setExcludeFromFeeLoading(true)
          try {
            // @ts-ignore
            await tokenExcludeContract.methods
              .excludeFromFee(router.params.id)
              .send({
                from: account,
              })
            message.success('Exclude from Fee Successfully.')
          } catch (e) {
            if (e && e.message) {
              message.error(e.message)
            }
          } finally {
            setExcludeFromFeeLoading(false)
          }
        })()
  }, [tokenExcludeContract, account, pool, router.params.id])
  const excludeFromMaxTx = useCallback(() => {
    if (
      !tokenExcludeContract ||
      !account ||
      account !== pool?.owner ||
      !router.params.id
    )
      return
        ; (async () => {
          setExcludeFromMaxTxLoading(true)
          try {
            // @ts-ignore
            await tokenExcludeContract.methods
              .setExcludeFromMaxTx(router.params.id, true)
              .send({
                from: account,
              })
            message.success('Exclude from Max Transaction Successfully.')
            setIsExcludedFromMaxTx(false)
          } catch (e) {
            if (e && e.message) {
              message.error(e.message)
            }
          } finally {
            setExcludeFromMaxTxLoading(false)
          }
        })()
  }, [
    tokenExcludeContract,
    account,
    pool,
    router.params.id,
    setIsExcludedFromMaxTx,
  ])

  const excludeFromReward = useCallback(() => {
    if (
      !tokenExcludeContract ||
      !account ||
      account !== pool?.owner ||
      !router.params.id
    )
      return
        ; (async () => {
          setExcludeFromRewardLoading(true)
          try {
            // @ts-ignore
            await tokenExcludeContract.methods
              .excludeFromReward(router.params.id)
              .send({
                from: account,
              })
            message.success('Exclude from Reward Successfully.')
          } catch (e) {
            if (e && e.message) {
              message.error(e.message)
            }
          } finally {
            setExcludeFromRewardLoading(false)
          }
        })()
  }, [tokenExcludeContract, account, pool, router.params.id])

  const finish = useCallback(() => {
    if (!poolContract || !account || account !== pool?.owner) return
      ; (async () => {
        setFinishLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.finalize().send({
            from: account,
          })
          message.success('Pool Was Finished')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setFinishLoading(false)
        }
      })()
  }, [poolContract, account, pool])

  const withdrawVestingToken = useCallback(() => {
    if (!poolContract || !account) return
      ; (async () => {
        setWithdrawalLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.withdrawVestingToken().send({
            from: account,
          })
          message.success('Withdraw successfully!')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setWithdrawalLoading(false)
        }
      })()
  }, [poolContract, account])

  const setKyc = useCallback(() => {
    if (!poolContract || !account || account !== pool?.governance) return
      ; (async () => {
        setKYCLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.updateCompletedKyc(true).send({
            from: account,
          })
          message.success('KYC Successfully.')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setKYCLoading(false)
        }
      })()
  }, [poolContract, account, pool])

  const removeKyc = useCallback(() => {
    if (!poolContract || !account || account !== pool?.governance) return
      ; (async () => {
        setKYCLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.updateCompletedKyc(false).send({
            from: account,
          })
          message.success('KYC Successfully.')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setKYCLoading(false)
        }
      })()
  }, [poolContract, account, pool])

  const cancel = useCallback(() => {
    if (!poolContract || !account || account !== pool?.owner) return
      ; (async () => {
        setCancelLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.cancel().send({
            from: account,
          })
          message.success('Pool Canceled')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setCancelLoading(false)
        }
      })()
  }, [poolContract, account, pool])

  const withdrawLeftovers = useCallback(() => {
    if (!poolContract || !account || account !== pool?.owner) return
      ; (async () => {
        setWithdrawalLeftOver(true)
        try {
          // @ts-ignore
          await poolContract.methods.withdrawLeftovers().send({
            from: account,
          })
          message.success('Withdraw Successfully.')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setWithdrawalLeftOver(false)
        }
      })()
  }, [poolContract, account, pool])

  const withdrawLiquidity = useCallback(() => {
    if (!poolContract || !account || account !== pool?.owner) return
      ; (async () => {
        setWithdrawalLiquidity(true)
        try {
          // @ts-ignore
          await poolContract.methods.withdrawLiquidity().send({
            from: account,
          })
          message.success('Withdraw Successfully.')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setWithdrawalLiquidity(false)
        }
      })()
  }, [poolContract, account, pool])

  const withdrawContribution = useCallback(() => {
    if (!poolContract || !account) return
      ; (async () => {
        setWithdrawalLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.withdrawContribution().send({
            from: account,
          })
          message.success('Withdrawal Contribution Successfully.')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setWithdrawalLoading(false)
        }
      })()
  }, [poolContract, account])

  const claim = useCallback(() => {
    if (!poolContract || !account) return
      ; (async () => {
        setClaimLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.claim().send({
            from: account,
          })
          message.success('Claim Token Successfully')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setClaimLoading(false)
        }
      })()
  }, [poolContract, account])

  const handleSubmit = useCallback(() => {
    if (!amount || Number(amount) === 0 || !poolContract || !account) return
      ; (async () => {
        setSubmitLoading(true)
        try {
          // @ts-ignore
          await poolContract.methods.contribute().send({
            from: account,
            value: Web3.utils.toWei(amount),
          })
          message.success('Purchase Successfully.')
        } catch (e) {
          if (e && e.message) {
            message.error(e.message)
          }
        } finally {
          setSubmitLoading(false)
        }
      })()
  }, [amount, poolContract, account])
  const maxPurchase = useMemo(() => {
    if (!pool || !maxAmount) return false
    // return Number(
    //   Web3.utils.fromWei(
    //     new BN(pool?.maxContribution || '0').sub(
    //       new BN(pool?.contributionOf || '0')
    //     ).toString()
    //   )
    // );
    return Web3.utils.fromWei(maxAmount)
  }, [pool, maxAmount]);

  // const trustScore = useMemo(() => {
  //   if (!pool || !token || !nativeToken) return null;
  //   const trusted = new AppService.trustScore({
  //     isTokenMintByPinksale: token.isTokenGenerated,
  //     refundType: pool.refundType,
  //     maxBuy: pool.maxContribution,
  //     hardcap: pool.hardCap,
  //     listingRate: pool.liquidityListingRate,
  //     presaleRate: pool.rate,
  //     liquidityPercentage: pool.liquidityPercent,
  //     liquidityLockupTime: isTestnet ? `${Number(pool.liquidityLockDays) / 60}` : `${Number(pool.liquidityLockDays) / 86400}`,
  //     totalVestingToken: token.poolBalance || '0',
  //     totalToken: token.totalSupply,
  //     vestingTime: isTestnet ? `${Number(pool.tgeLockDuration) / 60}` : `${Number(pool.tgeLockDuration) / 86400}`,
  //     additionalInfo: {
  //       name: token.name,
  //       logo: pool.poolDetails?.logoUrl || '',
  //       description: pool.poolDetails?.description || '',
  //       website: pool.poolDetails?.website || '',
  //       twitter: pool.poolDetails?.twitter || '',
  //       github: pool.poolDetails?.github || '',
  //       telegram: pool.poolDetails?.telegram || '',
  //     },
  //     nativeToken: nativeToken.symbol,
  //     nativeTokenDecimals: nativeToken.decimals,
  //     tokenSymbol: token.symbol,
  //     tokenDecimals: Number(token.decimals),
  //   });

  //   return trusted.scores();
  // }, [pool, token, nativeToken, isTestnet]);

  const allowPurchase = useMemo(() => {
    if (!pool) return false
    if (!amount || !minAmount || !maxAmount || !convertAmount) return false
    if (minAmount === '0' && maxAmount === '0') return false
    if (pool.version === 2 && typeof pool.isUserWhitelisted !== 'undefined') {
      return pool.isUserWhitelisted
    }
    try {
      const min = new BN(minAmount)
      const max = new BN(maxAmount)
      const bigNumAmount = new BN(Web3.utils.toWei(amount))
      if (bigNumAmount.gt(max)) return false
      if (bigNumAmount.lt(min)) return false
      return convertAmount !== '0'
    } catch (e) {
      return false
    }
  }, [pool, amount, convertAmount, minAmount, maxAmount])

  const withdrawable = useMemo(() => {
    if (!pool) return false
    const raised = Number(Web3.utils.fromWei(pool.totalRaised || '0'))
    const sCap = Number(Web3.utils.fromWei(pool.softCap || '0'))
    const refunded = Number(Web3.utils.fromWei(pool.refundedOf || '0'))
    const now = +new Date() / 1000
    const end = Number(pool.endTime)
    return (
      (pool.poolState === 2 ||
        (pool.poolState === 0 && now >= end && raised < sCap)) &&
      refunded === 0 &&
      Number(pool.contributionOf) > 0
    )
  }, [pool])

  const finishable = useMemo(() => {
    if (!pool || !account || account !== pool.owner) return false
    if (pool.poolState === 1) return false
    if (pool.status === 'canceled') return false
    const raised = Number(Web3.utils.fromWei(pool.totalRaised || '0'))
    const sCap = Number(Web3.utils.fromWei(pool.softCap || '0'))
    const hCap = Number(Web3.utils.fromWei(pool.hardCap || '0'))
    return raised >= hCap || (raised >= sCap && pool.status === 'ended')
  }, [pool, account])

  const withdrawLeftOverable = useMemo(() => {
    if (!pool || !account || account !== pool.owner) return false
    const raised = Number(Web3.utils.fromWei(pool.totalRaised || '0'))
    const sCap = Number(Web3.utils.fromWei(pool.softCap || '0'))
    return raised < sCap && pool.status === 'ended'
  }, [pool, account])

  const withdrawLiquidityable = useMemo(() => {
    if (!pool || !account || account !== pool.owner || !liquidityBalance)
      return false
    if (liquidityBalance && liquidityBalance === '0') return false
    const now = +new Date()
    const unlockTime = Number(pool.liquidityUnlockTime || 0)
    return unlockTime && now > unlockTime * 1000
  }, [pool, account, liquidityBalance])
  const poolDetails = pool?.poolDetails || {};
  const listingFee = useMemo(() => {
    if (!pool || !token) return null
    return calcListingFee({
      tokenDecimals: token.decimals,
      tokenFeePercent: pool.tokenFeePercent || '0',
      hardCap: Number(Web3.utils.fromWei(pool.hardCap || '0')),
      presaleRate: Number(
        new BN(pool.rate || 0)
          .div(new BN(10).pow(new BN(token.decimals)))
          .toString(),
      ),
      listingRate: Number(
        new BN(pool.liquidityListingRate || 0)
          .div(new BN(10).pow(new BN(token.decimals)))
          .toString(),
      ),
      liquidityPercentage: Number(pool.liquidityPercent || 0),
    })
  }, [pool, token])

  const tokenByAmount = useMemo(() => {
    if (!pool || !amount || !token?.decimals || !convertAmount) return null
    try {
      return etherJsFormat(convertAmount, Number(token.decimals))
    } catch (e) {
      return 0
    }
  }, [pool, amount, token?.decimals, convertAmount])
  const tokenByContributionOf = useMemo(() => {
    if (!pool || !pool.contributionOf || !token) return null
    try {
      const num = new BN(pool.contributionOf || '0')
        .mul(new BN(pool.rate))
        .div(new BN(10).pow(new BN(token.decimals)))
        .toString()
      return Web3.utils.fromWei(num)
    } catch (e) {
      return 0
    }
  }, [pool, token])
  const unsoldToken = useMemo(() => {
    if (!pool?.totalRaised || !listingFee || !pool.rate || !token?.decimals)
      return '0'
    try {
      const totalTokenSale = listingFee.totalSale
      const totalTokenSold = new BN(pool.rate)
        .mul(new BN(pool?.totalRaised))
        .div(new BN('10').pow(new BN('18')))
      const totalTokenUnsold = new BN(totalTokenSale).sub(totalTokenSold)
      return totalTokenUnsold
        .div(new BN('10').pow(new BN(token.decimals.toString())))
        .toString()
    } catch (e) {
      return '0'
    }
  }, [listingFee, pool?.totalRaised, pool?.rate, token?.decimals])

  const getLiquidityLockDays = useMemo(() => {
    if (!pool?.liquidityLockDays) return 0
    if (isTestnet) {
      return Number(pool.liquidityLockDays) / 60 // minutes
    }
    return Number(pool.liquidityLockDays) / 24 / 60 / 60
  }, [isTestnet, pool?.liquidityLockDays])

  const hasTab = useMemo(() => {
    if (pool && pool.owner === account) return true
    if (pool && pool.governance === account) return true
    return false
  }, [account, pool])
  useEffect(() => {
    if (!tokenExcludeContract || !router.params.id || account !== pool?.owner) return;
    (async () => {
        try {
          const isExclude = await tokenExcludeContract.methods
            .isExcludedFromMaxTx(router.params.id)
            .call()
          setIsExcludedFromMaxTx(isExclude)
        } catch (e) {
          // console.log(e);
        }
      })()
  }, [tokenExcludeContract, router.params.id, account, pool?.owner])

  useEffect(() => {
    if (!poolContract || !account) return
      ; (async () => {
        try {
          const purchaseAmount = await poolContract.methods
            .getContributionAmount(account)
            .call()
          setMinAmount(purchaseAmount['0'])
          setMaxAmount(purchaseAmount['1'])
          if (amount) {
            const converted = await poolContract.methods
              .convert(Web3.utils.toWei(amount))
              .call()
            setConvertAmount(converted)
          }
        } catch (e) {
          //
        }
      })()
  }, [poolContract, amount, account]);
  usePoolViewer(router.params.id);
  useEffect(() => {
    if (!poolContract || !account) return
    clearInterval(timer)
    timer = setInterval(() => {
      ; (async () => {
        try {
          const purchaseAmount = await poolContract.methods
            .getContributionAmount(account)
            .call()
          setMinAmount(purchaseAmount['0'])
          setMaxAmount(purchaseAmount['1'])
        } catch (e) {
          //
        }
      })()
    }, 2000)
    return () => {
      clearInterval(timer)
    }
  }, [poolContract, account])

  useEffect(() => {
    if (!poolContract || !pool?.liquidityUnlockTime) return
    clearInterval(liquidityTimer)
    liquidityTimer = setInterval(() => {
      ; (async () => {
        try {
          const now = +new Date() / 1000
          const end = Number(pool.liquidityUnlockTime)
          if (now < end) return
          const bl = await poolContract.methods.liquidityBalance().call()
          setLiquidityBalance(bl)
        } catch (e) {
          //
        }
      })()
    }, 2000)
    return () => {
      clearInterval(liquidityTimer)
    }
  }, [poolContract, pool?.liquidityUnlockTime])
  useEffect(() => {
    clearInterval(timerUpdateId)
    timerUpdateId = setInterval(() => {
      setUpdateId((prev) => prev + 1)
    }, 2500)

    return () => {
      clearInterval(timerUpdateId)
    }
  }, []);
  useEffect(() => {
    window.scroll(0, 0);
  }, []);
  if ((blacklist || []).indexOf(router.params.id) > -1) return null
  return (
    <div className="py-6 container">
      <div className="columns mt-4">
        <div className="column is-flex-grow-2">
          <Card>
            <article className="media">
              <figure className="media-left">
                <p className="image is-64x64">
                  <img
                    style={{
                      filter: imageError ? 'grayscale(1)' : 'grayscale(0)',
                    }}
                    src={imageError ? Logo : `${poolDetails.logoUrl}`}
                    alt=""
                    onError={() => setImageError(true)}
                  />
                </p>
              </figure>
              <div className="media-content">
                <div className="content">
                  <div className="is-flex is-align-items-center">
                    <div className="is-flex-grow-1 is-flex is-align-items-center single-title">
                      <h1 className="title mr-2">
                        {token ? `${token.name} Launchpad` : <SmallLoading />}
                      </h1>
                      {pool?.completedKyc && (
                        <span className="tag is-success">
                          <MdVerifiedUser />
                          <span className="ml-1">Verified</span>
                        </span>
                      )}
                    </div>
                    {pool?.owner === account && (
                      <Link
                        to={getPath(RouteNames.EditLauchpad).replace(
                          ':id',
                          router.params.id,
                        )}
                        className="is-size-5"
                      >
                        <AiOutlineEdit />
                      </Link>
                    )}
                  </div>
                  <div className="is-flex mt-1 mb-2">
                    {poolDetails.website ? (
                      <a
                        href={poolDetails.website}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <FiGlobe />
                      </a>
                    ) : null}
                    {poolDetails.twitter ? (
                      <a
                        href={poolDetails.twitter}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <FiTwitter />
                      </a>
                    ) : null}
                    {poolDetails.facebook ? (
                      <a
                        href={poolDetails.facebook}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <RiFacebookCircleLine />
                      </a>
                    ) : null}
                    {poolDetails.telegram ? (
                      <a
                        href={poolDetails.telegram}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <FaTelegramPlane />
                      </a>
                    ) : null}
                    {poolDetails.github ? (
                      <a
                        href={poolDetails.github}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <FiGithub />
                      </a>
                    ) : null}
                    {poolDetails.instagram ? (
                      <a
                        href={poolDetails.instagram}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <AiOutlineInstagram />
                      </a>
                    ) : null}
                    {poolDetails.discord ? (
                      <a
                        href={poolDetails.discord}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <FaDiscord />
                      </a>
                    ) : null}
                    {poolDetails.reddit ? (
                      <a
                        href={poolDetails.reddit}
                        rel="nofollow noreferrer"
                        target="_blank"
                        className="mr-4 is-size-5"
                      >
                        <AiOutlineReddit />
                      </a>
                    ) : null}
                  </div>
                  <p style={{ wordBreak: 'break-all' }}>
                    {poolDetails.description}
                  </p>
                </div>
              </div>
            </article>

            <div className="table-container mt-6">
              <table>
                <tbody>
                  <tr>
                    <td>Presale Address</td>
                    <td className="has-text-right">
                      <a
                        href={`${explorerDomain}/address/${router.params.id || ''
                          }`}
                        target="_blank"
                        rel="noreferrer nofollow"
                      >
                        {tokenLoading ? <SmallLoading /> : router.params.id}
                      </a>
                    </td>
                  </tr>
                  <tr>
                    <td>Token Address</td>
                    <td className="has-text-right">
                      <a
                        className="mr-1"
                        href={`${explorerDomain}/token/${pool?.token || ''}`}
                        target="_blank"
                        rel="noreferrer nofollow"
                      >
                        {pool?.token}
                      </a>
                      {token && token.isTokenGenerated && (
                        <Tooltip title="This token was generated from PinkSale">
                          <AiOutlineCheck style={{ color: 'green' }} />
                        </Tooltip>
                      )}
                      <br />
                      <span>
                        (Do not send {nativeToken.symbol} to the token address!)
                      </span>
                    </td>
                  </tr>
                  <tr>
                    <td>Token Name</td>
                    <td className="has-text-right">
                      {tokenLoading ? <SmallLoading /> : token?.name}
                    </td>
                  </tr>
                  <tr>
                    <td>Token Symbol</td>
                    <td className="has-text-right">
                      {tokenLoading ? <SmallLoading /> : token?.symbol}
                    </td>
                  </tr>
                  <tr>
                    <td>Token Decimals</td>
                    <td className="has-text-right">
                      {tokenLoading ? <SmallLoading /> : token?.decimals}
                    </td>
                  </tr>
                  <tr>
                    <td>Total Supply</td>
                    <td className="has-text-right">
                      {tokenLoading ? (
                        <SmallLoading />
                      ) : (
                        <>
                          {token?.totalSupply
                            ? etherJsFormat(
                              token?.totalSupply,
                              Number(token?.decimals || 0),
                            )
                            : null}{' '}
                          {token?.symbol}
                        </>
                      )}
                    </td>
                  </tr>
                  <tr>
                    <td>Tokens For Presale</td>
                    <td className="has-text-right">
                      {tokenLoading ? (
                        <SmallLoading />
                      ) : (
                        <>
                          {listingFee?.totalSale
                            ? etherJsFormat(
                              listingFee?.totalSale,
                              Number(token?.decimals || 0),
                            )
                            : null}{' '}
                          {token?.symbol}
                        </>
                      )}
                    </td>
                  </tr>
                  <tr>
                    <td>Presale Rate</td>
                    <td className="has-text-right">
                      1 {nativeToken.symbol} ={' '}
                      {etherJsFormat(
                        pool?.rate || '0',
                        Number(token?.decimals || 0),
                      )}{' '}
                      {token?.symbol || ''}
                    </td>
                  </tr>
                  <tr>
                    <td>Listing Rate</td>
                    <td className="has-text-right">
                      1 {nativeToken.symbol} ={' '}
                      {etherJsFormat(
                        pool?.liquidityListingRate || '0',
                        Number(token?.decimals || 0),
                      )}{' '}
                      {token?.symbol || ''}
                    </td>
                  </tr>
                  <tr>
                    <td>Soft Cap</td>
                    <td className="has-text-right">
                      {loading ? (
                        <SmallLoading />
                      ) : (
                        `${Web3.utils.fromWei(pool?.softCap || '0')} ${nativeToken.symbol
                        }`
                      )}
                    </td>
                  </tr>
                  <tr>
                    <td>Hard Cap</td>
                    <td className="has-text-right">
                      {loading ? (
                        <SmallLoading />
                      ) : (
                        `${Web3.utils.fromWei(pool?.hardCap || '0')} ${nativeToken.symbol
                        }`
                      )}
                    </td>
                  </tr>
                  <tr>
                    <td>Unsold Tokens</td>
                    <td className="has-text-right">
                      {pool?.refundType === '0' ? 'Refund' : 'Burn'}
                    </td>
                  </tr>
                  <tr>
                    <td>Presale Start Time</td>
                    <td className="has-text-right">
                      {loading ? (
                        <SmallLoading />
                      ) : (
                        formatDate(
                          getUTCTimeByOffset(Number(pool?.startTime || 0)),
                        )
                      )}{' '}
                      (UTC)
                    </td>
                  </tr>
                  <tr>
                    <td>Presale End Time</td>
                    <td className="has-text-right">
                      {loading ? (
                        <SmallLoading />
                      ) : (
                        formatDate(
                          getUTCTimeByOffset(Number(pool?.endTime || 0)),
                        )
                      )}{' '}
                      (UTC)
                    </td>
                  </tr>
                  <tr>
                    <td>Liquidity Percent</td>
                    <td className="has-text-right">
                      {loading ? (
                        <SmallLoading />
                      ) : (
                        `${pool?.liquidityPercent || 0}%`
                      )}
                    </td>
                  </tr>
                  {Number(pool?.liquidityUnlockTime) !== 0 &&
                    pool?.poolState === 1 ? (
                    <tr>
                      <td>Liquidity Lockup Time</td>
                      <td className="has-text-right">
                        {loading ? (
                          <SmallLoading />
                        ) : (
                          <>
                            {pool?.poolState === 1
                              ? `${formatDate(
                                getUTCTimeByOffset(
                                  Number(pool?.liquidityUnlockTime || 0),
                                ),
                              )} UTC (${moment(
                                Number(pool?.liquidityUnlockTime || 0) * 1000,
                              ).fromNow()})`
                              : null}
                          </>
                        )}
                      </td>
                    </tr>
                  ) : (
                    <tr>
                      <td>Liquidity Lockup Time</td>
                      <td className="has-text-right">
                        {loading ? (
                          <SmallLoading />
                        ) : (
                          <>
                            {getLiquidityLockDays}{' '}
                            {!isTestnet ? 'days' : 'minutes'} after pool ends
                          </>
                        )}
                      </td>
                    </tr>
                  )}
                  {pool?.totalVestingTokens &&
                    pool?.totalVestingTokens !== '0' ? (
                    <tr>
                      <td>Total vesting Tokens</td>
                      <td className="has-text-right">
                        {pool?.totalVestingTokens
                          ? etherJsFormat(
                            pool?.totalVestingTokens,
                            Number(token?.decimals || 0),
                          )
                          : null}{' '}
                        {token?.symbol}
                      </td>
                    </tr>
                  ) : null}
                  {pool?.tgeTime && pool?.tgeTime !== '0' ? (
                    <tr>
                      <td>TGE Time</td>
                      <td className="has-text-right">
                        {formatDate(
                          getUTCTimeByOffset(Number(pool?.tgeTime || 0)),
                        )}{' '}
                        UTC
                      </td>
                    </tr>
                  ) : null}
                  {pool?.tgeLockDuration && pool?.tgeLockDuration !== '0' ? (
                    <tr>
                      <td>
                        First token release after listing (
                        {!isTestnet ? 'days' : 'minutes'})
                      </td>
                      <td className="has-text-right">
                        {!isTestnet
                          ? Number(pool?.tgeLockDuration) / 60 / 60 / 24
                          : Number(pool?.tgeLockDuration) / 60}
                      </td>
                    </tr>
                  ) : null}
                  {pool?.cycle && pool?.cycle !== '0' ? (
                    <tr>
                      <td>Cycle ({!isTestnet ? 'days' : 'minutes'})</td>
                      <td className="has-text-right">
                        {!isTestnet
                          ? Number(pool?.cycle) / 60 / 60 / 24
                          : Number(pool?.cycle) / 60}
                      </td>
                    </tr>
                  ) : null}
                  {pool?.tgeTokensRelease && pool?.tgeTokensRelease !== '0' ? (
                    <tr>
                      <td>First token release amount</td>
                      <td className="has-text-right">
                        {pool?.tgeTokensRelease
                          ? etherJsFormat(
                            pool?.tgeTokensRelease,
                            Number(token?.decimals || 0),
                          )
                          : null}{' '}
                        {token?.symbol}
                      </td>
                    </tr>
                  ) : null}
                  {pool?.tokensReleaseEachCycle &&
                    pool?.tokensReleaseEachCycle !== '0' ? (
                    <tr>
                      <td>Tokens release each cycle</td>
                      <td className="has-text-right">
                        {pool?.tokensReleaseEachCycle
                          ? etherJsFormat(
                            pool?.tokensReleaseEachCycle,
                            Number(token?.decimals || 0),
                          )
                          : null}{' '}
                        {token?.symbol}
                      </td>
                    </tr>
                  ) : null}
                  {pool?.totalVestedTokens &&
                    pool?.totalVestedTokens !== '0' ? (
                    <tr>
                      <td>Total vested tokens</td>
                      <td className="has-text-right">
                        {pool?.totalVestedTokens
                          ? etherJsFormat(
                            pool?.totalVestedTokens,
                            Number(token?.decimals || 0),
                          )
                          : null}{' '}
                        {token?.symbol}
                      </td>
                    </tr>
                  ) : null}
                </tbody>
              </table>
              {pool?.totalVestingTokens && pool?.totalVestingTokens !== '0' && (
                <VestingTable
                  totalVestingTokens={pool?.totalVestingTokens}
                  tgeTime={
                    pool?.tgeTime && pool?.tgeTime !== '0'
                      ? pool?.tgeTime
                      : pool?.endTime
                  }
                  tgeTokensRelease={pool?.tgeTokensRelease}
                  cycle={pool?.cycle}
                  tokensReleaseEachCycle={pool?.tokensReleaseEachCycle}
                  totalVestedTokens={pool?.totalVestedTokens}
                  tokenDecimals={token?.decimals}
                  isEstimate={!(pool.tgeTime && pool.tgeTime !== '0')}
                />
              )}
              {pool && pool.version === 2 && Number(pool.numberOfWhitelistedUsers) > 0 && (
                <WhitelistTable
                  numberOfWhitelistedUsers={Number(
                    pool.numberOfWhitelistedUsers,
                  )}
                  address={router.params.id}
                  isOwner={account === pool?.owner}
                />
              )}
            </div>
          </Card>
          <div style={{ height: 24 }} />
          <Card>
            {token && (
              <DiscussionEmbed
                shortname="pinksale"
                config={{
                  url: 'https://pinksale.finance' + router.url,
                  identifier: router.params.id,
                  title: token.name,
                  language: 'en_US', //e.g. for Traditional Chinese (Taiwan)
                }}
              />
            )}
          </Card>
        </div>
        <div className="column is-flex-grow-1">
          {/* {trustScore ? (
            <>
              <TrustScoreTable data={trustScore} />
              <div style={{ height: 24 }} />
            </>
          ) : null} */}
          {pool ? (
            <Card>
              <form>
                {pool?.status === 'inprogress' && (
                  <div className="has-text-centered">
                    <p className="mb-2">Presale Ends In</p>
                    <Countdown
                      date={Number(pool?.endTime || 0) * 1000}
                      renderer={rendererCountdown}
                    />
                  </div>
                )}
                {pool?.status === 'incoming' && (
                  <div className="has-text-centered">
                    <p className="mb-2">Presale Starts In</p>
                    <Countdown
                      date={Number(pool?.startTime || 0) * 1000}
                      renderer={rendererCountdown}
                    />
                  </div>
                )}
                <div className="pb-4">
                  <progress
                    className="progress is-success mt-4 mb-2"
                    value={
                      pool.hardCap && Number(pool.hardCap) !== 0
                        ? (Number(pool.totalRaised) / Number(pool.hardCap)) *
                        100
                        : 0
                    }
                    max="100"
                  >
                    {pool.hardCap && Number(pool.hardCap) !== 0
                      ? (Number(pool.totalRaised) / Number(pool.hardCap)) * 100
                      : 0}
                    %
                  </progress>
                  <div className="is-flex is-align-items-center is-size-7">
                    <div className="is-flex-grow-1">
                      {pool ? Web3.utils.fromWei(pool.totalRaised || '0') : 0}{' '}
                      {nativeToken.symbol}
                    </div>
                    <div className="is-flex-grow-1 has-text-right">
                      {pool ? Web3.utils.fromWei(pool.hardCap || '0') : 0}{' '}
                      {nativeToken.symbol}
                    </div>
                  </div>
                </div>
                {pool?.status === 'inprogress' && (
                  <>
                    <div className="field">
                      <label className="label">
                        Amount (max: {maxPurchase} {nativeToken.symbol})
                      </label>
                      <div className="control">
                        <input
                          className="input"
                          type="number"
                          placeholder="0.0"
                          value={amount}
                          onChange={handleAmountChange}
                        />
                        {tokenByAmount && allowPurchase ? (
                          <p className="help is-info">
                            You received: {tokenByAmount} {token?.symbol}
                          </p>
                        ) : null}
                      </div>
                    </div>

                    <Button
                      onClick={handleSubmit}
                      primary
                      type="button"
                      loading={submitLoading}
                      disabled={
                        !amount || Number(amount) === 0 || !allowPurchase
                      }
                    >
                      <AiOutlineCheck />
                      <span className="ml-2">
                        {pool && pool.isUserWhitelisted === false
                          ? "You're not in whitelist"
                          : 'Buy'}
                      </span>
                    </Button>
                  </>
                )}
                {pool?.status === 'ended' && (
                  <div className="has-text-centered">
                    This pool has been ended.
                  </div>
                )}
                {pool?.status === 'canceled' && (
                  <div className="has-text-centered">
                    This pool has been canceled.
                  </div>
                )}
                {pool?.status === 'ended' &&
                  pool &&
                  pool?.poolState === 1 &&
                  Number(pool.contributionOf) > 0 &&
                  Number(pool.claimedOf) <= 0 && (
                    <div className="has-text-centered mt-4">
                      <Button primary onClick={claim} loading={claimLoading}>
                        Claim token{' '}
                        {tokenByContributionOf
                          ? `(${tokenByContributionOf} ${token?.symbol})`
                          : ''}
                      </Button>
                    </div>
                  )}
                {withdrawable && (
                  <div className="has-text-centered mt-4">
                    <Button
                      primary
                      onClick={withdrawContribution}
                      loading={withdrawalLoading}
                    >
                      Withdrawal contribution
                    </Button>
                    <p className="mt-2">Since the soft cap was not reached.</p>
                  </div>
                )}
                {Number(pool?.withdrawableTokens || 0) > 0 &&
                  account === pool?.owner && (
                    <div className="has-text-centered mt-4">
                      <Button
                        primary
                        onClick={withdrawVestingToken}
                        loading={withdrawalLoading}
                      >
                        Withdrawal vesting token
                      </Button>
                    </div>
                  )}
              </form>
            </Card>
          ) : null}
          <div style={{ height: 24 }} />
          <Card>
            {hasTab && (
              <div className="tabs is-centered">
                <ul>
                  <li className={tab === 'info' ? 'is-active' : ''}>
                    <a onClick={() => setTab('info')}>Info</a>
                  </li>
                  <li className={tab === 'owner' ? 'is-active' : ''}>
                    <a onClick={() => setTab('owner')}>Owner zone</a>
                  </li>
                  {pool && pool.governance === account && (
                    <li className={tab === 'dev' ? 'is-active' : ''}>
                      <a onClick={() => setTab('dev')}>Dev zone</a>
                    </li>
                  )}
                </ul>
              </div>
            )}
            {tab === 'info' && (
              <div className="table-container">
                <table>
                  <tbody>
                    <tr>
                      <td>Status</td>
                      <td className="has-text-right has-text-primary">
                        {loading ? <SmallLoading /> : pool?.status}
                      </td>
                    </tr>
                    {pool &&
                      pool.version === 2 &&
                      typeof pool.useWhitelisting !== 'undefined' && (
                        <tr>
                          <td>Sale type</td>
                          <td className="has-text-right has-text-primary">
                            {pool.useWhitelisting ? 'Whitelist Only' : 'Public'}
                          </td>
                        </tr>
                      )}
                    <tr>
                      <td>Minimum Buy</td>
                      <td className="has-text-right">
                        {loading ? (
                          <SmallLoading />
                        ) : (
                          `${Web3.utils.fromWei(
                            pool?.minContribution || '0',
                          )} ${nativeToken.symbol}`
                        )}
                      </td>
                    </tr>
                    <tr>
                      <td>Maximum Buy</td>
                      <td className="has-text-right">
                        {loading ? (
                          <SmallLoading />
                        ) : (
                          `${Web3.utils.fromWei(
                            pool?.maxContribution || '0',
                          )} ${nativeToken.symbol}`
                        )}
                      </td>
                    </tr>
                    {pool?.contributionOf ? (
                      <tr>
                        <td>Your Purchased</td>
                        <td className="has-text-right">
                          {loading ? (
                            <SmallLoading />
                          ) : (
                            `${Web3.utils.fromWei(
                              pool?.contributionOf || '0',
                            )} ${nativeToken.symbol}`
                          )}
                        </td>
                      </tr>
                    ) : null}
                    {pool?.poolState === 1 ? (
                      <tr>
                        <td>Unsold token</td>
                        <td className="has-text-right">
                          {loading ? (
                            <SmallLoading />
                          ) : (
                            `${unsoldToken} ${token?.symbol}`
                          )}
                        </td>
                      </tr>
                    ) : null}
                  </tbody>
                </table>
              </div>
            )}
            {tab === 'owner' && pool && account === pool?.owner && (
              <>
                {pool.version === 2 && pool.useWhitelisting && (
                  <WhitelistAction address={router.params.id} />
                )}
                {Boolean(finishable) && (
                  <div className="my-2">
                    <Button
                      danger
                      fullwidth
                      onClick={finish}
                      loading={finishLoading}
                    >
                      Finish
                    </Button>
                  </div>
                )}
                {pool?.poolState === 0 && (
                  <div className="my-2">
                    <Button
                      danger
                      fullwidth
                      onClick={cancel}
                      loading={cancelLoading}
                    >
                      Cancel
                    </Button>
                  </div>
                )}
                {typeof isExcludedFromMaxTx !== 'undefined' &&
                  !isExcludedFromMaxTx && (
                    <div className="my-2">
                      <Button
                        danger
                        fullwidth
                        onClick={excludeFromMaxTx}
                        loading={excludeFromMaxTxLoading}
                      >
                        Exclude from Max Transaction
                      </Button>
                    </div>
                  )}
                {showExcludeFromFee && (
                  <div className="my-2">
                    <Button
                      danger
                      fullwidth
                      onClick={excludeFromFee}
                      loading={excludeFromFeeLoading}
                    >
                      Exclude from Fee
                    </Button>
                  </div>
                )}
                {showExcludeFromReward && (
                  <div className="my-2">
                    <Button
                      danger
                      fullwidth
                      onClick={excludeFromReward}
                      loading={excludeFromRewardLoading}
                    >
                      Exclude from Reward
                    </Button>
                  </div>
                )}
                {Boolean(withdrawLeftOverable) && (
                  <div className="my-2">
                    <Button
                      danger
                      fullwidth
                      onClick={withdrawLeftovers}
                      loading={withdrawalLeftOver}
                    >
                      Withdraw Leftovers
                    </Button>
                  </div>
                )}
                {Boolean(withdrawLiquidityable) && (
                  <div className="my-2">
                    <Button
                      danger
                      fullwidth
                      onClick={withdrawLiquidity}
                      loading={withdrawalLiquidity}
                    >
                      Withdraw Liquidity
                    </Button>
                  </div>
                )}
              </>
            )}
            {tab === 'dev' &&
              pool &&
              pool?.governance &&
              account === pool?.governance && (
                <>
                  {pool?.completedKyc ? (
                    <div className="my-2">
                      <Button
                        fullwidth
                        onClick={removeKyc}
                        loading={KYCLoading}
                      >
                        Remove KYC
                      </Button>
                    </div>
                  ) : (
                    <div className="my-2">
                      <Button fullwidth onClick={setKyc} loading={KYCLoading}>
                        Set KYC
                      </Button>
                    </div>
                  )}
                </>
              )}
          </Card>
        </div>
      </div>
    </div>
  )
}

export default PoolDetailPage
