import { useEffect, useState } from "react";
import Web3 from "web3";

import { useBnbBusdContract } from "./useContract";

export default function useWbnbPrice() {
  const contract = useBnbBusdContract();
  const [price, setPrice] = useState<number>(0);
  useEffect(() => {
    if (!contract) return;
    (async () => {
      try {
        const { _reserve0, _reserve1 } = await contract.methods
          .getReserves()
          .call();
        const wbnb = Number(Web3.utils.fromWei(_reserve0));
        const usdt = Number(Web3.utils.fromWei(_reserve1));
        if (usdt > 0) {
          setPrice(usdt / wbnb);
        }
      } catch (e) {
        //
      }
    })();
  }, [contract]);
  return price;
}
