import { useEffect } from "react";
import { useState } from "react";

import { useWeb3 } from "../packages/web3/hooks";
import { useStandardTokenContract } from "./useContract";

export default function useTokenFee() {
  const web3 = useWeb3();
  const contract = useStandardTokenContract(web3);
  const [fee, setFee] = useState<string>();

  useEffect(() => {
    if (!contract) return;
    (async () => {
      try {
        const f = await contract.methods.flatFee().call();
        setFee(f);
      } catch (e) {
        // console.log(e);
      }
    })();
  }, [contract]);
  return fee;
}
