import { Web3Provider } from "@ethersproject/providers";
import Web3 from "web3";

import { ConnectorNames, POLLING_INTERVAL } from "./constants";
import { injected, walletconnect } from "./connectors";
import IcWalletConnect from "./images/walletconnect.svg";
import IcSafePad from "./images/safepad.svg";
import IcTrustWallet from "./images/trustwallet.svg";
import ChainIds from "../../configs/chainIds";

const IcMetamask =
  "data:image/svg+xml;base64,";

const rpcUrls = [
  "https://bsc-dataseed1.ninicoin.io",
  "https://bsc-dataseed1.defibit.io",
  "https://bsc-dataseed.binance.org",
];

const bscTestnetRpcUrls = [
  "https://data-seed-prebsc-1-s1.binance.org:8545",
  "https://data-seed-prebsc-2-s1.binance.org:8545",
  "https://data-seed-prebsc-1-s2.binance.org:8545",
];

export function getRpcUrl() {
  return rpcUrls[Math.floor(Math.random() * rpcUrls.length)];
}

export function getTestnetRpcUrl() {
  return bscTestnetRpcUrls[
    Math.floor(Math.random() * bscTestnetRpcUrls.length)
  ];
}

// @ts-ignore
export function getLibrary(provider: any): Web3Provider {
  const library = new Web3Provider(provider);
  library.pollingInterval = POLLING_INTERVAL;
  return library;
}

type TConnector = {
  name: string;
  provider: any;
  icon: string;
};

export function getConnectorsByName(): { [key: string]: TConnector } {
  return {
    [ConnectorNames.Injected]: {
      name: "Metamask",
      icon: IcMetamask,
      provider: injected,
    },
    [ConnectorNames.TrustWallet]: {
      name: "TrustWallet",
      icon: IcTrustWallet,
      provider: injected,
    },
    [ConnectorNames.WalletConnect]: {
      name: "WalletConnect",
      icon: IcWalletConnect,
      provider: walletconnect,
    },
    [ConnectorNames.SafePad]: {
      name: "SafePad Wallet",
      icon: IcSafePad,
      provider: injected,
    },
  };
}

export async function requestBscNetwork() {
  const provider = (window as WindowChain).ethereum;
  if (provider) {
    const chainId = ChainIds.BSC_MAINNET;
    try {
      // @ts-ignore
      await provider.request({
        method: "wallet_addEthereumChain",
        params: [
          {
            chainId: `0x${chainId.toString(16)}`,
            chainName: "Binance Smart Chain Mainnet",
            nativeCurrency: {
              name: "BNB",
              symbol: "bnb",
              decimals: 18,
            },
            rpcUrls,
            blockExplorerUrls: [`https://bscscan.com/`],
          },
        ],
      });
      return true;
    } catch (error) {
      return false;
    }
  } else {
    console.error(
      "Can't setup the BSC network on metamask because window.ethereum is undefined"
    );
    return false;
  }
}

const httpProvider = new Web3.providers.HttpProvider(
  process.env.REACT_APP_PHASE === "production"
    ? getRpcUrl()
    : getTestnetRpcUrl(),
  {
    timeout: 10000,
  }
);
const web3NoAccount = new Web3(httpProvider);

export function getWeb3NoAccount() {
  return web3NoAccount;
}

export function getContract(abi: any, address: string, web3?: Web3) {
  const _web3 = web3 ?? web3NoAccount;
  return new _web3.eth.Contract(abi, address);
}
