import { FC, memo, useMemo } from "react";
import { Collapse } from 'antd';
import Web3 from 'web3';
import range from 'lodash/range';
import chunk from 'lodash/chunk';
import { useState } from "react";
import { Pagination } from 'antd';

import { etherJsFormat, formatDate, getUTCTimeByOffset } from "../../utils/helper";

type Props = {
  totalVestingTokens?: string;
  tgeTime?: string;
  tgeTokensRelease?: string;
  cycle?: string;
  tokensReleaseEachCycle?: string;
  totalVestedTokens?: string;
  tokenDecimals?: string | number;
  isEstimate?: boolean;
}

// @ts-ignore
const BN = Web3.utils.BN;
const { Panel } = Collapse;

const VestingTable: FC<Props> = ({
  totalVestingTokens,
  tgeTime,
  tgeTokensRelease,
  cycle,
  tokensReleaseEachCycle,
  totalVestedTokens,
  tokenDecimals,
  isEstimate,
}) => {
  const [page, setPage] = useState<number>(1);
  const periods = useMemo(() => {
    const first = {
      num: 1,
      tokenRelease: tgeTokensRelease,
      time: tgeTime,
    };
    const remainTokensAfterFirstRelease = new BN(totalVestingTokens).sub(
      new BN(tgeTokensRelease)
    );
    const numOfCycle = remainTokensAfterFirstRelease.div(
      new BN(tokensReleaseEachCycle)
    );
    const cycles = range(Math.floor(parseInt(numOfCycle.toString(), 10)));
    const remainPeriods = cycles.map((v) => {
      return {
        num: v + 2,
        tokenRelease: tokensReleaseEachCycle,
        time: first.time && first.time !== '0' ? Number(first.time) + (Number(cycle) * (v + 1)) : null,
      };
    });
    const released = new BN(tokensReleaseEachCycle).mul(numOfCycle);
    if (released.lt(remainTokensAfterFirstRelease)) {
      return [
        first,
        ...remainPeriods,
        {
          num: remainPeriods.length + 2,
          tokenRelease: remainTokensAfterFirstRelease.sub(released).toString(),
          time: first.time && first.time !== '0' ? Number(first.time) + (Number(cycle) * (remainPeriods.length + 1)) : null,
        }
      ];
    }
    return [
      first,
      ...remainPeriods,
    ];
  }, [
    tgeTokensRelease,
    tgeTime,
    tokensReleaseEachCycle,
    totalVestingTokens,
    cycle,
  ]);
  const data = useMemo(() => {
    const split = chunk(periods, 20);
    return split[page - 1] || [];
  }, [periods, page]);
  const onPageChange = (page: number) => {
    setPage(page);
  };
  return (
    <Collapse ghost expandIconPosition="right">
      <Panel header={`Anti-Rug info ${isEstimate ? '(Estimate from end time)' : '(Official)'}`} key="1">
        <table className="has-text-centered">
          <thead>
            <tr>
              <th>Unlock #</th>
              <th>Time (UTC)</th>
              <th>Unlocked tokens</th>
            </tr>
          </thead>
          <tbody>
            {data.map(({ num, time, tokenRelease }) => (
              <tr key={num}>
                <td>{num}</td>
                <td>{time && time !== '0' ? formatDate(getUTCTimeByOffset(Number(time))) : '-'}</td>
                <td>{etherJsFormat(tokenRelease || '0', Number(tokenDecimals || 0))}</td>
              </tr>
            ))}
          </tbody>
        </table>
        {totalVestedTokens && totalVestedTokens !== '0' && (
          <table>
            <tbody>
              <tr>
                <td>Total vested tokens</td>
                <td className="has-text-centered">{etherJsFormat(totalVestedTokens || '0', Number(tokenDecimals || 0))}</td>
              </tr>
            </tbody>
          </table>
        )}
        <div className="mt-4 has-text-centered">
          <Pagination
            defaultCurrent={1}
            pageSize={20}
            total={periods.length}
            showSizeChanger={false}
            onChange={onPageChange}
          />
        </div>
      </Panel>
    </Collapse>
  );
};

export default memo(VestingTable);
